// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'font-awesome/css/font-awesome.min.css'
import './assets/css/base.css'
import './assets/css/icon.css'
import AlloyFinger from 'alloyfinger'
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger_vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css'
import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'
let options = {
    fullscreenEl: false, //控制是否显示右上角全屏按钮
    closeEl: false, //控制是否显示右上角关闭按钮
    tapToClose: true, //点击滑动区域应关闭图库
    shareEl: false, //控制是否显示分享按钮
    zoomEl: false, //控制是否显示放大缩小按钮
    counterEl: false, //控制是否显示左上角图片数量按钮
    arrowEl: true,  //控制如图的左右箭头（pc浏览器模拟手机时）
    tapToToggleControls: true, //点击应切换控件的可见性
    clickToCloseNonZoomable: true //点击图片应关闭图库，仅当图像小于视口的大小时
};
Vue.use(preview, options)
Vue.use(ElementUI);
Vue.use(AlloyFingerPlugin, {
    AlloyFinger
})
Vue.config.productionTip = false
var Api, BMap, vueObj, Trans, fs, zip;
/* eslint-disable no-new */

window.apiready = function () {
    Api = api;

    // 定位功能
    var t = setTimeout(function () {
        // BmLocation = api.require('bmLocation');
        // Vue.prototype.BmLocation = BmLocation;
        Vue.prototype.BMap = api.require('bMap');

    }, 2000);

    // 图片转base64
    Trans = api.require('trans');
    Vue.prototype.Trans = Trans;


    //创建本地的数据库
    fs = api.require('fs');
    Vue.prototype.fs = fs;

    zip = api.require('zip');
    Vue.prototype.zip = zip;

    vueObj = new Vue({
        el: '#app',
        router,
        components: { App },
        template: '<App/>'
    }); 

}






















