<template>
    <div class="case-detail-body">
        <div class="case-detail-title">案例详情标题文字</div>
        <ul class="case-detail-ul">
            <li>
                项目成果类型:{{projectAchievementType}}
            </li>
            <li>
                规划成果类型:{{projectAchievementType}}
            </li>
            <li>
                矢量成果类型:{{projectAchievementType}}
            </li>
        </ul>
        <ul class="case-detail-ul">
            <li>规划成果:{{projectAchievementType}}</li>
            <li>项目名称:{{projectAchievementType}}</li>
            <li>项目阶段:{{projectAchievementType}}</li>
            <li>行政区代码:{{projectAchievementType}}</li>
            <li>行政区名称:{{projectAchievementType}}</li>
        </ul>
        <ul class="case-detail-ul">
            <li>规划开始:{{projectAchievementType}}</li>
            <li>规划结束:{{projectAchievementType}}</li>
            <li>联系电话:{{projectAchievementType}}</li>
            <li>联系地址:{{projectAchievementType}}</li>
        </ul>
    </div>
</template>

<script>
export default {
  data () {
    return {
        projectAchievementType:"XXXXXXXXXXXXXXXXXXXX",
    }
  },
  methods:{}
}
</script>
<style>
    body{
        background:#f2f4f5;
    }
    .case-detail-body{
        color:#000;
    }
    .case-detail-title{
        color:#393939;
        font-size:20px;
        text-align: left;
        text-indent:10%;
        padding-top:26px;
        padding-bottom:15px;
        margin-top:15px;
        background:#fff;
    }
    .case-detail-ul{
        text-align: left;
        text-indent:10%;
        background:#fff;
        box-shadow:0 3px 5px #ddd;
        margin-bottom:18px;
    }
    .case-detail-ul > li{
        height:45px;
        line-height:45px;
        color:#333;
        font-size:14px;
        text-align: left;
        text-indent:10%;
        background:#fff;
        border-bottom:1px solid #ccc;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .case-detail-ul > li:last-child{
        border:none;
    }
</style>



