<template>
  <div class="login">
    <div class="login-bg">
      <img src="data:image/png;base64,












" width="100%" />
      <div class="title">欢迎登录</div>
    </div>
    <div class="input-box">
      <div class="act-box" :class="{ 'bb-r': isActMiss }">
        <span class="login-act-ico"></span>
        <input id="userName" type="text" :class="{ 'alert-r': isActMiss }" placeholder="请输入用户名/手机号" autocomplete="off" v-model="userNameVal" />
      </div>
      <div class="psw-box" :class="{ 'bb-r': isPswMiss }">
        <span class="login-psw-ico"></span>
        <input id="psw" type="password" :class="{ 'alert-r': isPswMiss }" placeholder="请输入6-16位密码" autocomplete="off" v-model="pswVal" />
      </div>
    </div>
    <div class="forgot-psw cl">
      <a href="javascript:void(0)" class="rember-psw fl">
        <input id="choose" type="checkbox" :checked="remberPswVal" @click="remberPsw()" />
        <span class="remberPswTxt">记住密码</span>
      </a>
      <a href="javascript:void(0)" class="fr" @click="forgot()">忘记密码</a>
    </div>
    <div class="btns">
      <a href="javascript:void(0)" @click="login()">登录</a>
      <a href="javascript:void(0)" @click="register()">注册</a>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { hostUrl, artMsg } from "../common/common";

export default {
  data() {
    return {
      isActMiss: false,
      isPswMiss: false,
      userNameVal: "",
      pswVal: "",
      remberPswVal: false,
    };
  },
  mounted() {
    this.getUserAndPsw();
  },
  methods: {
    login() {
      console.log("login");
      var _this = this;
      var userName = _this.userNameVal;
      userName = userName.trim();
      var psw = _this.pswVal;
      psw = psw.trim();
      var postUrl = hostUrl + "login";
      _this.isActMiss = _this.isPswMiss = false;
      if (!userName) {
        _this.isActMiss = true;
      } else if (!psw) {
        _this.isPswMiss = true;
      } else {
        if (api.connectionType !== "none") {
          axios.post(postUrl, {
            loginName: userName,
            password: psw,
          }).then(function (response) {
            if (response.data.status) {
              localStorage.token = response.data.objectData.token;
              localStorage.userName = userName;
              localStorage.userId = response.data.objectData.userInfo.userId;
              localStorage.psw = psw;
              localStorage.remberPsw = $("#choose").is(":checked");
              console.log(localStorage.remberPsw);
              // _this.remberPswVal = true;
              console.log("login:success");
              _this.$router.push({ path: "../main" });
            } else {
              artMsg(response.data.message);
            }
          })
            .catch(function (error) {
              console.log(error);
            });
        } else {
          if (userName == localStorage.userName && psw == localStorage.psw) {
            _this.$router.push({ path: "../main" });
          } else {
            artMsg('用户名或密码不正确');
          }

        }

      }
    },
    register() {
      this.$router.push({ path: "/register" });
    },
    forgot() {
      this.$router.push({ path: "/forgot" });
    },
    // 记住密码

    // remberPsw(){
    //     if(this.remberPswVal){
    //         // 反选
    //         this.userNameVal = "";
    //         this.pswVal = "";
    //     }
    //     else{
    //         // 选中
    //         this.userNameVal = !!localStorage.userName ?  localStorage.userName : ""
    //         this.pswVal = !!localStorage.psw ? localStorage.psw : ""
    //     }
    //     this.remberPswVal = !this.remberPswVal;
    // },
    remberPsw() {
      if ($("#choose").is(":checked")) {
        this.remberPswVal = true;
      } else {
        this.remberPswVal = false;
      }
    },
    // 获取记住密码的状态以及本地账号密码回显
    getUserAndPsw() {
      if (localStorage.remberPsw == "true") {
        this.remberPswVal = true;
        this.userNameVal = localStorage.userName;
        this.pswVal = localStorage.psw;
      } else {
        this.remberPswVal = false;
        this.userNameVal = "";
        this.pswVal = "";
      }
    },
    chkRemberPsw() {
      if (this.remberPswVal) {
        this.userNameVal = localStorage.userName;
        this.pswVal = localStorage.psw;
      }
    },
  },
};
</script>
    <style>
.login {
  position: relative;
  width: 100%;
}
.title {
  position: absolute;
  top: 70px;
  width: 100%;
  color: #fff;
  text-align: center;
  font-size: 24px;
  z-index: 9;
}
.input-box {
  width: calc(100% - 31.48%);
  margin: 85px 15.74% 0 15.74%;
}

.forgot-psw {
  margin-top: 16px;
  margin-right: 15.74%;
  margin-bottom: 30px;
}

.forgot-psw > a {
  color: #6eb1ff;
  font-size: 14px;
  text-decoration: none;
}

.login-act-ico {
  position: relative;
  top: 2px;
  width: 20px;
  height: 20px;
  background-image: url(data:image/png;base64,);
  display: inline-block;
  margin-right: 12.5px;
}

.login-psw-ico {
  position: relative;
  top: 2px;
  width: 20px;
  height: 24px;
  background-image: url(data:image/png;base64,);
  display: inline-block;
  margin-right: 12.5px;
}

.act-box {
  margin-bottom: 33px;
}

.act-box,
.psw-box {
  padding-bottom: 15px;
  border-bottom: 1px solid #d6d6d6;
}

.act-box input,
.psw-box input {
  width: 70%;
  height: 100%;
  color: #989898;
  font-size: 16px;
  border: none;
  background: transparent;
}

.act-box input:focus,
.psw-box input:focus {
  outline: none;
}

.btns {
  margin: 0 9.72%;
}

.btns > a {
  width: 100%;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  border-radius: 25px;
  display: block;
}

.btns > a:first-child {
  height: 51px;
  line-height: 51px;
  color: #fff;
  background-image: linear-gradient(to right, #6fb2ff, #095fff);
}

.btns > a:last-child {
  height: 49px;
  line-height: 49px;
  color: #21a4fe;
  border: 1px solid #21a4fe;
  margin-top: 15px;
  background: #fff;
}
.alert-r::-webkit-input-placeholder {
  color: red;
}
.bb-r {
  border-bottom-color: red !important;
}
.rember-psw {
  margin-left: 19%;
}
.rember-psw > input {
  position: relative;
  top: 2px;
}
</style>



