<template>
    <div class="share-list">
		<div class="share-item" v-for="item in dataList">
			<div class="share-box cl">
                <div class="share-l fl" @click="showCase(item.planAchieveId,item.title)">
					<img :src="item.coverUrl" />
				</div>
				<div class="share-r fl" @click="goToDetail(item)">
					<div class="case-title">{{item.title}}</div>
					<div class="case-other">
						<div class="date fl">{{item.modifyDate}}</div>
						<div class="link-group fr">
							<a href="javascript:void(0)">
								<span class="dwn-btn-ico"></span>
							</a>
							<a href="javascript:void(0)">
								<span class="share-btn-ico"></span>
							</a>
							<a href="javascript:void(0)">
								<span class="share-type-ico"></span>
							</a>
							<a href="javascript:void(0)">
								<span class="wx_ico"></span>
							</a>
							<a href="javascript:void(0)">
								<span class="friends-ico"></span>
							</a>
						</div>
					</div>
				</div>
			</div>
		</div>
    </div>    
</template>

<script>
import axios from 'axios';
import {hostUrl,artMsg} from '../common/common';
export default {
  data () {
    return {
        dataList:[]
	}
  },
  created(){
      this.getData();
  },
  methods:{
    // 案例详情
    goToDetail(data){
    //   this.$router.push({path:'./case/detail'});
        this.$router.push({
            name:'caseDetail',
            query:{
                routeParams:data
            }
        });
    },
    // 案例展示
    showCase(id,title){
        // this.$router.push({path:'./case/show'});
        this.$router.push({
            name:'caseShow',
            query:{
                id:id,
                title:title
            }
        });
    },
    getData(){
        var _this = this;
        var postUrl = hostUrl + "case/list";

        axios({
            method: 'post',
            url: postUrl,
            data:{
                "isCase": "1",
                "pageNumber": "1",
                "pageSize": "20",
                "timestamp": ""
            },
            headers:{
                token:localStorage.token
            }
        })
        .then(function (response) {
            if(response.data.status){
                // console.log("case:");
                // console.log(response);
                _this.dataList = response.data.objectData.pageList;
            }
            else{
                artMsg(response.data.message);
            }
        })
        .catch(function (error) {
            console.log(error);
        });
    },
  }
}
</script>
<style>
    .fl {
        float: left!important;
    }
    .fr {
        float: right!important;
    }
    .cl:after {
        visibility: hidden;
        display: block;
        font-size: 0;
        content: " ";
        clear: both;
        height: 0
    }

    .cl {
        *zoom: 1
    }
    .share-list{
        background-color:#f9fbfd;
    }
    .share-list .share-item{
        width:calc(100% - 40px);
        background:#fff;
        margin:26px 20px 0 20px;
        border-radius:7px;
        display:block;
        overflow:hidden;
        box-shadow: 0 3px 10px #ddd;
    }
    .share-list .share-box{
        padding: 20px 10px 20px 30px;
        zoom: 0.86;
    }
    .share-list .share-l{
        width:115px;
        height:115px;
        border-radius:7px;
        overflow:hidden;
    }
    .share-list .share-l > img{
        width:100%;
        height:100%;
    }
    .share-list .case-title{
        color:#666;
        font-size:16px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
    }
    .share-list .share-l{
        margin-right:14px;
    }
    .share-list .share-r{
        position:relative;
        width: 58%;
        height: 115px;
    }
    .share-list .case-other{
        position:absolute;
        right:0;
        bottom:0;
        width: 100%;
    }
    .share-list .link-group>a{
        margin-right: 2px;
        text-decoration: none;
    }
    .share-list .date{
        position: relative;
        top: 2px;
        color:#999;
        font-size:12px;
    }
</style>



