<template>
  <div class="search-item">
    <div class="search-box">
      <a href="javascript:void(0)" @click="searchBtnClick()">
        <span class="search-btn"></span>
      </a>
      <input id="searchBtn" placeholder="请输入关键字搜索" @focus="searchBtnFocus()" />
    </div>
    <ul class="search-list" v-show="searchListDispFlg">
      <li @click="selectItem('邵庄村')">邵庄村</li>
      <li @click="selectItem('临汾市15县')">临汾市15县</li>
    </ul>
  </div>
</template>

<script>
import { hostUrl, artMsg, CoordinateUtil } from './common/common';
export default {
  props: ['parentHandler'],
  data() {
    return {
      mercator: [13345930.793, 4343316.725],
      searchListDispFlg: false
    }
  },
  methods: {
    // input获得焦点
    searchBtnFocus() {
      var tar = document.getElementById("searchBtn");
      tar.value = "";
      this.searchListDispFlg = !this.searchListDispFlg;
    },
    // 搜索按下
    searchBtnClick() {
      var tar = document.querySelector("#searchBtn");
      var val = tar.value;
      val = val.trim();
      if (!!val) {
        this.search(val);
      }
      else {
        artMsg("暂无数据");
        tar.setAttribute("placeholder", "请输入关键字搜索");
      }
    },
    // 搜索
    search(val) {
      if (val == '邵庄村') {
        if (this.$parent.isOffline) {
           this.$parent.initOfflineSzcLayer();
        } else {
          if(this.$parent.$options.name == 'main'){
          	this.$router.push({path:'/map'});
          }
          else{
          	this.$parent.addMarker(this.mercator);
          }
        }


      }
      else if (val == '临汾市15县') {
        var lat = 111.558872;
        var lon = 36.122902;
        var tmpObj = [111.558872, 36.122902];
        // var tmpObj = CoordinateUtil.getWgs84xy(lon,lat);
        this.$parent.setViewCenter(tmpObj);
        // this.$parent.view.setZoom(16);
      }
      else {
        var tar = document.querySelector("#searchBtn");
        artMsg("暂无数据");
        tar.setAttribute("placeholder", "请输入关键字搜索");
      }
    },
    // 选定搜索项
    selectItem(val) {
      this.searchListDispFlg = false;
      this.search(val);
    },
    // 
  }
}
</script>
<style>
.search-item {
  position: absolute;
  top: 15px;
  right: 6.4%;
  left: 6.4%;
  width: 100%;
  z-index: 99990;
}
.search-box {
  width: calc(100% - 12.8%);
  height: 38px;
  line-height: 38px;
  color: #95a0b6;
  text-align: left;
  font-size: 18px;
  border-radius: 7px;
  background: rgba(255, 255, 255, 0.5) !important;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
.search-box > a {
  color: #007bfe;
  font-size: 22px;
  margin-right: 13px;
  margin-left: 23px;
}
.search-box > input {
  position: relative;
  top: -2px;
  width: 75%;
  height: 100%;
  border: none;
  background: transparent;
}
.search-btn {
  position: relative;
  top: 4px;
}
.search-list {
  position: relative;
  top: 5px;
  width: calc(100% - 12.8%);
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}
.search-list > li {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px dashed #eee;
}
</style>



