<template>
    <div class="forgot-box">
		<div class="forgot-title">用户注册</div>
		<div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">用户名&nbsp;&nbsp;&nbsp;</span>
                <input id="userName" type="text" placeholder="请设置用户名" />
            </div>
        </div>
        <div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">设置密码</span>
                <input id="password" type="password" placeholder="6-20位数字、英文组成" />
            </div>
        </div>
        <div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">确认密码</span>
                <input id="confirmPsw" type="password" placeholder="和设置密码一样" />
            </div>
        </div>
		<div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">部门&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <select id="department">
                    <option value="0">请选择部门</option>
                    <option value="1">部门1</option>
                    <option value="2">部门2</option>
                </select>
            </div>
        </div>
        <div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">管理辖区</span>
                <select id="area">
                    <option value="0">请选择辖区</option>
                    <option value="1">辖区1</option>
                    <option value="2">辖区2</option>
                </select>
            </div>
        </div>
		<div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">姓名&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <input id="name" type="text" placeholder="请输入姓名" />
            </div>
        </div>
        <div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">电话&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <input id="telPhoneNum" type="phone" placeholder="请输入电话号码" />
            </div>
        </div>
		<div class="input-box">
            <div class="act-box">
                <span class="mail-addr-title">电子邮箱</span>
                <input id="email" type="email" placeholder="请输入电子邮箱" />
            </div>
        </div>
        <div class="btns forgot-btn">
            <a href="javascript:void(0)" @click="registerNow()">立即注册</a>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import {hostUrl,artMsg} from '../common/common';
export default {
  data () {
    return {}
  },
  methods:{
      registerNow(){
        var _this = this;
        var postUrl = hostUrl + "register";
        var userName = document.getElementById("userName").value;
        userName = userName.trim();

        var password = document.getElementById("password").value;
        password = password.trim();

        var confirmPsw = document.getElementById("confirmPsw").value;
        confirmPsw = confirmPsw.trim(); 
        
        var department = document.getElementById("department").value;
        var area = document.getElementById("area").value

        var name = document.getElementById("name").value;
        name = name.trim();

        var telPhoneNum = document.getElementById("telPhoneNum").value;
        telPhoneNum = telPhoneNum.trim();

        var email = document.getElementById("email").value;
        email = email.trim(); 

        if(!userName){
            artMsg("请输入用户名");
            return;
        }
        else if(!password){
            artMsg("请设置密码");
            return;
        }
        else if(password != confirmPsw){
            artMsg("请确认两次密码输入一致");
            return;
        }       
        else{
            axios.post(
                postUrl, {
                "areaId": area,
                "departmentId": department,
                "email": email,
                "password": password,
                "phone": telPhoneNum,
                "realName": name,
                "timestamp": "",
                "userName": userName 
            })
            .then(function (response) {
                // console.log(response);
                // console.log(response.data.status)
                if(response.data.status){
                    artMsg("注册成功");
                    _this.$router.push({path:'/'});
                }
                else{
                    artMsg(response.data.message);
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        }        
      }
  }
}
</script>
<style>
    .forgot-box {
        position: relative;
        margin-right:6.6%;
        margin-left:6.6%;
    }
    .input-box {
        width: 100%;
        margin: 10px 0;
    }
    .act-box,
    .psw-box {
        padding-bottom: 15px;
        border-bottom: 1px solid #d6d6d6;
    }
    .act-box input,
    .psw-box input,
    .act-box select,
    .psw-box select{
        width: 55%;
        height: 100%;
        color: #989898;
        font-size: 16px;
        background:transparent;
        border: none;
        outline:none;
    }
    
    .act-box input:focus,
    .psw-box input:focus {
        outline: none;
    }
    .btns{
        margin-top:20px;
    }
    .btns>a {
        width: 100%;
        height: 51px;
        line-height: 51px;
        color: #fff;
        font-size: 16px;
        text-align: center;
        text-decoration: none;
        border-radius: 25px;
        background-image: linear-gradient(to right, #6fb2ff, #095fff);
        display: block;
    }
    .mail-addr-title{
        color:#000;
        font-size:20px;
        margin-right: 20px;
        margin-left: 10px;
    }
    .back-btn{
        width:23px;
        height:19px;
        background-image:url(data:image/png;base64,);
        margin-top:20px;
    }
    .forgot-title{
        color:#020433;
        font-size:30px;
        text-align: left;
        text-indent: 25px;
        margin-top:28px;
        margin-bottom:35px;
    }
    .forgot-prompt{
        color:#404b69;
        font-size:14px;
        margin-top:26px;
        margin-bottom:90px;
    }
    .forgot-btn>a{
        height: 51px;
        line-height: 51px;
        color: #fff;
        background-image: -webkit-gradient(linear, left top, right top, from(#6fb2ff), to(#095fff));
        background-image: linear-gradient(to right, #6fb2ff, #095fff);
    }
</style>
       


