<template>
  <div>
    <div class="history-box" v-show="historyBoxDispFLg && !historyDetailDispFLg">
      <div>
        <div class="title-box cl">
          <div class="third-title fl">历史调研管理</div>
          <!-- <div class="new-box fr">
                    <span class="new-btn-circle-20"></span>
                    <span class="new-btn-txt">新增</span>
                </div> -->
        </div>
        <div class="search-content">
          <span class="search-18-btn" @click="getDataList(1)"></span>
          <input id="keywordValue" type="text" placeholder="请输入关键字搜索" />
        </div>
        <mescroll-vue v-if="historyBoxDispFLg" ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit">
          <ul id="historyList" class="history-list" ref="list">
            <li class="cl" v-for="(item, index) in historyList" @click="getDataDetail(item.historyId)">
              <div class="history-list-image-box fl">
                <img :src="item.thumbnailUrl" />
              </div>
              <div class="history-info fr">
                <div>{{ item.superviseDesc }}</div>
                <div class="cl">
                  <div class="fl">{{ item.modifyDate }}</div>
                  <div class="history-item-btns fr">
                    <a href="javascript:void(0)" class="history-item-edit" @click.stop="editHistoryItem(item.historyId)">
                      <span class="edit-btn-14"></span>
                    </a>
                    <a href="javascript:void(0)" class="history-item-delte" @click.stop="historyListDel(index, item.historyId)">
                      <span class="del-btn-14"></span>
                    </a>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </mescroll-vue>
      </div>
      <a href="javascript:void(0)" class="close-menu-btn" @click="hideHistoryWin()"><i class="close-16-ico"></i></a>
    </div>
    <div class="history-box" v-if="historyDetailDispFLg">
      <div>
        <div class="title-box cl">
          <div class="third-title fl">村民监督详情</div>
        </div>
        <div class="text-left color-999">
          <div v-if="historyDetail.uploadUserName">上报人：{{ historyDetail.uploadUserName }}</div>
          <div v-if="historyDetail.uploadDate">发布日期：{{ historyDetail.uploadDate }}</div>
          <div v-if="historyDetail.superviseType">
            监督类型：{{ historyDetail.superviseType.name?historyDetail.superviseType.name: historyDetail.superviseTypeName}}
          </div>
          <div>描述：{{ historyDetail.superviseDesc }}</div>
          <div>
            图片：
            <el-row :gutter="20">
              <el-col :span="6" v-for="(item, index) in historyDetail.imageUrl" :key="index">
                <img :src="item" class="history-list-image" preview="1" />
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
      <a href="javascript:void(0)" class="close-menu-btn" @click="hideHistoryDetailWin()"><i class="close-16-ico"></i></a>
    </div>
    <div class="history-box" v-if="localHistoryBoxDispFLg&&!historyDetailDispFLg">
      <div>
        <div class="title-box cl">
          <div class="third-title fl">草稿管理</div>
        </div>
        <div style="height: 280px; overflow: auto;">
          <ul id="historyList" class="history-list" ref="list">
            <li class="cl" v-for="(item, index) in localHistoryList" @click="getLocalDataDetail(item)">
              <div class="history-list-image-box fl">
                <img v-if="item.thumbnailUrl" :src="item.thumbnailUrl" />
                <img v-if="!item.thumbnailUrl" :src="item.imageList[0]" />
              </div>
              <div class="history-info fr">
                <div>{{item.superviseDesc}}</div>
                <div class="cl">
                  <div class="fl">{{item.createDate}}</div>
                  <div class="history-item-btns fr">
                    <!-- <a href="javascript:void(0)" class="history-item-edit" @click.stop="editLocalHistoryItem(item)">
                      <span class="edit-btn-14"></span>
                    </a> -->
                    <a href="javascript:void(0)" class="history-item-delte" @click.stop="localhistoryListDel(index)">
                      <span class="del-btn-14"></span>
                    </a>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <a href="javascript:void(0)" class="close-menu-btn" @click="hideLocalHistoryWin()"><i class="close-16-ico"></i></a>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { hostUrl, artMsg } from "../common/common";
import { Polygon } from "ol/geom";
import Feature from "ol/Feature";
import bus from "../common/bus.js";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
  name: "history",
  props: ["parentHandler"],
  components: {
    MescrollVue,
  },
  data() {
    return {
      localHistoryList: [],
      localHistoryBoxDispFLg: false,
      historyBoxDispFLg: false,
      clickHandDrawnFlg: false,
      clickCircleDrawnFlg: false,
      clickPolygonDrawnFlg: false,
      historyList: [],
      historyDetail: "",
      historyDetailDispFLg: false,
      pageNumber: 1,
      defaultImg:
        "data:image/jpg;base64,",

      mescroll: null, // mescroll实例对象
      mescrollDown: {
        // 下拉刷新
        auto: false,
        callback: this.downCallback,
      },
      mescrollUp: {
        // 上拉加载
        auto: false,
        page: {
          num: 1, //当前页 默认1,回调之前会加1; 即callback(page)会从2开始
          size: 10, //每页数据条数,默认10
        },
        callback: this.upCallback,
        htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
        noMoreSize: 5, //如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
        toTop: {
          //回到顶部按钮
          warpId: "historyList",
          warpClass: "mescroll-totop",
          src: require("../../assets/img/backTop.png"), //图片路径,默认null,支持网络图
          offset: 1000, //列表滚动1000px才显示回到顶部按钮
        },
        empty: {
          //列表第一页无任何数据时,显示的空提示布局; 需配置warpId才显示
          warpId: "historyList", //父布局的id (1.3.5版本支持传入dom元素)
          icon: "./static/mescroll/mescroll-empty.png", //图标,默认null,支持网络图
          tip: "暂无数据~", //提示
        },
      },
    };
  },
  // 定义组件过滤器
  filters: {
    superviseTypeFmt(val, location) {
      switch (location) {
        case "0":
          return "违法用地";
        case "1":
          return "侵占农田";
        case "2":
          return "河道污水";
        default:
          return "";
      }
    },
  },
  mounted() {
    this.initPreview()
  },
  methods: {
    initPreview() {

      this.$preview.on('close', () => {//close只是众多事件名的其中一个，更多请查看文档
        console.log('图片查看器被关闭')
      });
      // 添加图片查看器实例--this.$preview.self 注意：此实例仅在图片查看器被打开时生效
      this.$preview.on('imageLoadComplete', (e, item) => {
        console.log(this.$preview.self)  //此时this.$preview.self拥有原插件photoswipe文档中的所有方法和属性
      })
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll;
    },
    //下拉刷新的回调
    downCallback() {
      this.getDataList(1);
    },
    //上拉加载的回调
    upCallback(page) {
      this.getDataList(this.pageNumber);
    },

    //编辑详情
    editHistoryItem(hisId) {
      this.historyDetailDispFLg = false;
      var _this = this;
      _this.$parent.source.clear(); //清空手绘
      var postUrl = hostUrl + "supervise/detail";
      axios({
        method: "post",
        url: postUrl,
        data: {
          historyId: hisId,
          timestamp: "",
        },
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          if (response.data.status) {
            if (response.data.objectData.examineType.id !== "0") {
              artMsg(response.data.objectData.examineType.name + ",无法修改");
              return;
            }
            _this.historyBoxDispFLg = false;
            _this.historyDetail = response.data.objectData;
            const superviseInfo = JSON.parse(_this.historyDetail.superviseInfo);
            bus.$emit("clickHistoryItemEvent", _this.historyDetail);
            _this.$parent.handDraws = superviseInfo;
            if (superviseInfo && superviseInfo.length > 0) {
              superviseInfo.forEach((polygonArr) => {
                // 循环每一个手绘图形
                const myPolygon = new Polygon(polygonArr); //绘制多边形
                const feature = new Feature(myPolygon);
                _this.$parent.source.addFeature(feature);
              });
            }
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    // 查看详情
    getDataDetail(hisId) {
      var _this = this;
      _this.$parent.source.clear(); //清空手绘
      var postUrl = hostUrl + "supervise/detail";
      axios({
        method: "post",
        url: postUrl,
        data: {
          historyId: hisId,
          timestamp: "",
        },
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          if (response.data.status) {
            _this.historyDetailDispFLg = true;
            _this.historyDetail = response.data.objectData;
            console.log(_this.historyDetail.superviseType.name);
            const superviseInfo = JSON.parse(_this.historyDetail.superviseInfo);
            if (superviseInfo && superviseInfo.length > 0) {
              superviseInfo.forEach((polygonArr) => {
                // 循环每一个手绘图形
                const myPolygon = new Polygon(polygonArr); //绘制多边形
                const feature = new Feature(myPolygon);
                _this.$parent.source.addFeature(feature);
              });
            }
          } else {
            artMsg(response.data.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 查看本地离线详情
    getLocalDataDetail(data) {
      this.$parent.source.clear(); //清空手绘
      this.historyDetailDispFLg = true;
      this.historyDetail = data;
      const superviseInfo = JSON.parse(this.historyDetail.superviseInfo);
      if (superviseInfo && superviseInfo.length > 0) {
        superviseInfo.forEach((polygonArr) => {
          // 循环每一个手绘图形
          const myPolygon = new Polygon(polygonArr); //绘制多边形
          const feature = new Feature(myPolygon);
          this.$parent.source.addFeature(feature);
        });
      }
    },

    hideHistoryDetailWin() {
      this.historyDetailDispFLg = false;
      this.$parent.source.clear(); //清空手绘
    },

    // 删除历史记录
    historyListDel(index, hisId) {
      var _this = this;
      var postUrl = hostUrl + "supervise/delete";

      axios({
        method: "post",
        url: postUrl,
        data: {
          historyId: hisId,
          timestamp: "",
        },
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          if (response.data.status) {
            console.log(response);
            _this.historyList.splice(index, 1);
            artMsg("删除成功");
          } else {
            artMsg(response.data.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 删除本地离线历史记录
    localhistoryListDel(index) {
      console.log(this.localHistoryList)
      this.localHistoryList = this.localHistoryList.filter((item, i) => i !== index);
      localStorage.setItem("localHistoryList", JSON.stringify(this.localHistoryList));
    },

    // 显示 拍照上传 内容
    dispHistoryWin() {
      this.historyBoxDispFLg = true;
    },
    dispLocalHistoryWin() {
      this.localHistoryBoxDispFLg = true;
      if (localStorage.localHistoryList) {
        this.localHistoryList = JSON.parse(localStorage.localHistoryList)
      }

    },
    hideLocalHistoryWin() {
      this.localHistoryBoxDispFLg = false;
    },
    // 隐藏 拍照上传 内容
    hideHistoryWin() {
      this.$parent.clearSecondMenu();
      this.$parent.dispCtrlBox();
      this.$parent.dispMapZoom();
      this.$parent.historyManageFlg = false;
      this.historyBoxDispFLg = false;
      this.hideHistoryDetailWin();
    },
    // 取得数据
    getDataList(pageNumber) {
      var _this = this;
      if (pageNumber == 1) {
        // 如果是第一页需手动置空列表
        _this.historyList = [];
        _this.pageNumber = 1;
      }
      var postUrl = hostUrl + "supervise/history";
      var keywordValue = document.getElementById("keywordValue").value;
      keywordValue = keywordValue.trim();
      axios({
        method: "post",
        url: postUrl,
        data: {
          pageNumber: _this.pageNumber,
          pageSize: "10",
          superviseDesc: keywordValue,
        },
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          if (response.data.status) {
            console.log(response);
            if (
              response.data.objectData.pageList &&
              response.data.objectData.pageList.length > 0
            ) {
              const list = response.data.objectData.pageList;
              _this.historyList = [..._this.historyList, ...list];
              for (var i = 0; i < _this.historyList.length; i++) {
                _this.historyList[i].thumbnailUrl = !_this.historyList[i]
                  .thumbnailUrl
                  ? _this.defaultImg
                  : _this.historyList[i].thumbnailUrl;
              }
              _this.mescroll.setPageNum(_this.pageNumber); // 手动设置当前page.num的值
              _this.pageNumber++;
            }
            // 数据渲染成功后,隐藏下拉刷新的状态
            _this.$nextTick(() => {
              _this.mescroll.endBySize(
                _this.historyList.length,
                response.data.objectData.totalCount
              ); //必传参数(当前页的数据个数, 总数据量)
            });
          } else {
            artMsg(response.data.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
};
</script>
<style>
.history-box {
  position: absolute;
  right: 0;
  bottom: 58px;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.8);
}
.history-box > div {
  padding: 15px 3.7% 0 3.7%;
}
.new-box {
  position: relative;
  top: 7px;
}
.new-btn-circle-20 {
  position: relative;
  top: 2px;
}
.new-btn-txt {
  color: #346dfb;
  font-size: 15px;
  margin-left: 5px;
  display: inline-block;
}
.search-content {
  position: relative;
  height: 30px;
  line-height: 30px;
  background: #f7f7f7;
  border-radius: 5px;
  overflow: hidden;
}
.search-content .search-18-btn {
  position: absolute;
  top: 6px;
  left: 6px;
}
.search-content > input {
  width: 80%;
  height: 100%;
  color: #9a9a9a;
  font-size: 14px;
  border: none;
  background: #f7f7f7;
  margin-left: 16px;
}
/* .history-list {
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
} */
.history-list > li {
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #e9e9e9;
}
.history-list-image-box {
  width: 75px;
  height: 75px;
  border-radius: 8px;
  margin-right: 14px;
  overflow: hidden;
}
.history-list-image {
  width: 100%;
  height: 75px;
}
.history-list-image-box > img {
  width: 100%;
  height: 100%;
}
.history-info {
  position: relative;
  width: calc(100% - 90px);
  height: 75px;
  overflow: hidden;
}
.history-info > div:first-child {
  line-height: 20px;
  color: #666;
  font-size: 14px;
  text-align: left;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.history-info > div:last-child {
  margin-top: 20px;
}
.history-info > div:nth-child(2) > div:first-child {
  color: #999;
  font-size: 13px;
  text-align: left;
}
.history-item-btns > a:first-child {
  margin-right: 14px;
}
.text-left {
  text-align: left;
}
.color-999 {
  color: #999;
}
/*通过fixed固定mescroll的高度*/
.mescroll {
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
}
.mescroll-totop {
  bottom: 60px;
  right: 15px;
}
</style>



