<template>
  <div :class="$style.pdf" v-show="visible">
    <div @click="visible = false"></div>
    <div id="preViewPdf"></div>
  </div>
</template>
<script>
import Pdfh5 from "pdfh5";
import "pdfh5/css/pdfh5.css";
export default {
  name: "pdfH5",
  data() {
    return {
      visible: false,
      pdfh5: null,
    };
  },
  methods: {
    openPdf(url) {
      this.visible = true;
      try{
      this.pdfh5 = new Pdfh5("#preViewPdf", {
        pdfurl: url,
        lazy:true
      });
      } catch (error){
        console.log('err:',error)
        console.log('message: ', error.message);
      }
    },
  },
};
</script>

<style module>
* {
  padding: 0;
  margin: 0;
}

html,
body {
  width: 100%;
  height: 100%;
}

.pdf {
  position: fixed;
  top: 115px;
  left: 0;
  width: 100vw;
  height: 75vh;
  background: #000;
  overflow: hidden;
  /* z-index: ; */
}
</style>


