<template>
    <div class="case-detail-body">
        <div class="case-detail-title">{{detailData.title}}</div>
        <ul class="case-detail-ul">
            <li>
                项目成果类型:{{detailData.projectAchieveType}}
            </li>
            <li>
                规划成果类型:{{detailData.planAchieveType}}
            </li>
            <li>
                矢量成果类型:{{detailData.vectorAchieveType}}
            </li>
        </ul>
        <ul class="case-detail-ul">
            <li>规划成果:{{detailData.planAchieve}}</li>
            <li>项目名称:{{detailData.projectName}}</li>
            <li>项目阶段:{{detailData.projectStage}}</li>
            <li>行政区代码:{{detailData.regionCode}}</li>
            <li>行政区名称:{{detailData.regionName}}</li>
        </ul>
        <ul class="case-detail-ul">
            <li>规划开始:{{detailData.planStartDate}}</li>
            <li>规划结束:{{detailData.planEndDate}}</li>
            <li>联系电话:{{detailData.phone}}</li>
            <li>联系地址:{{detailData.address}}</li>
        </ul>
    </div>
</template>

<script>
export default {
  data () {
    return {
        detailData:{}
    }
  },
  methods:{},
  created(){
      this.detailData = this.$route.query.routeParams;
      // console.log(this.detailData);
  }
}
</script>
<style>
    body{
        background:#f2f4f5;
    }
    .case-detail-body{
        color:#000;
    }
    .case-detail-title{
        color:#393939;
        font-size:20px;
        text-align: left;
        text-indent:10%;
        padding-top:26px;
        padding-bottom:15px;
        margin-top:15px;
        background:#fff;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .case-detail-ul{
        text-align: left;
        text-indent:10%;
        background:#fff;
        box-shadow:0 3px 5px #ddd;
        margin-bottom:18px;
    }
    .case-detail-ul > li{
        height:45px;
        line-height:45px;
        color:#333;
        font-size:14px;
        text-align: left;
        text-indent:10%;
        background:#fff;
        border-bottom:1px solid #ccc;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .case-detail-ul > li:last-child{
        border:none;
    }
</style>



