<template>
    <div class="main">
        <div class="main-bg">
			<div class="swiper">
				<!-- Additional required wrapper -->
				<div class="swiper-wrapper">
					<!-- Slides -->
					<div class="swiper-slide" v-for="item in bgList">
						<img width="100%" :src="item.img" />
					</div>
				</div>
				<!-- If we need pagination -->
				<div class="swiper-pagination"></div>
				<!-- If we need scrollbar -->
			</div>
            <searchBox></searchBox>
            <!-- <div class="main-title">村庄规划信息聚合云平台</div> -->
        </div>
        <div class="menu cl">
            <div class="menu-link fl">
                <a href="javascript:void(0)" @click="caseShare()">
                    <span class="case-share-ico"></span>
                </a>
                <div class="menu-title">案例分享</div>
            </div>	
            <div class="menu-link fl">
                <a href="javascript:void(0)" @click="goToOneMap()">
                    <span class="planning-results-ico"></span>
                </a>
                <div class="menu-title">村民监督</div>
            </div>
            <div class="menu-link fl">
                <a href="javascript:void(0)" @click="goToMapPlan()">
                    <span class="topic-searche-ico"></span>
                </a>
                <div class="menu-title">规划选址</div>
            </div>
        </div>
        <div class="case-list cl">
            <div></div>
            <div>案例列表</div>
        </div>
		<div class="share-list">
			<div class="share-item" v-for="item in caseDataList">
				<div class="share-box cl">
					<div class="share-l fl" @click="showCase(item.planAchieveId,item.title)">
						<img :src="item.coverUrl" />
					</div>
					<div class="share-r fl" @click="goToDetail(item)">
						<div class="case-title">{{item.title}}</div>
						<div class="case-other">
							<div class="date fl">{{item.modifyDate}}</div>
							<div class="link-group fr">
								<a href="javascript:void(0)">
									<span class="dwn-btn-ico"></span>
								</a>
								<a href="javascript:void(0)">
									<span class="share-btn-ico"></span>
								</a>
								<a href="javascript:void(0)">
									<span class="share-type-ico"></span>
								</a>
								<a href="javascript:void(0)">
									<span class="wx_ico"></span>
								</a>
								<a href="javascript:void(0)">
									<span class="friends-ico"></span>
								</a>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>  
    </div>
</template>
<script>

import Swiper from 'swiper/swiper-bundle';
import 'swiper/swiper-bundle.css';

import searchBox from './searchBox';
import {hostUrl,artMsg} from './common/common';
import axios from 'axios';

export default {
	name:'main',
    components:{
		searchBox,
		Swiper
    },
	setup() {
      const onSwiper = (swiper) => {
        console.log(swiper);
      };
      const onSlideChange = () => {
        console.log('slide change');
      };
      return {
        onSwiper,
        onSlideChange,
      };
    },
    data () {
        return {
            // 去村民监督
            surpervison:true,
            mapplan:true,
            isActMiss:false,
            isPswMiss:false,
            caseDataList:[],
			bgList:[
				{
					img:"data:image/jpg;base64,"
				},
				{
					img:"data:image/jpg;base64,"
				},
				{
					img:"data:image/jpg;base64,"
				}
			],
        }
    },
	mounted(){
		this.initSwiper();
	},
	created(){
      this.getCaseListData();
  	},
    methods:{
		// 一张图
		goToOneMap(){
			     this.$router.push({
        path: "/map",
       query:{surpervison:this.surpervison},
      });
		},
        	goToMapPlan(){
			     this.$router.push({
        path: "/map",
       query:{mapplan:this.mapplan},
      });
		},
		// 案例分享
        caseShare(){
            this.$router.push({path:'/case/index'});
        },
		// 规划成果
		planning(){
			this.$router.push({path:'/planning/index'});
		},
		// 轮播初始化
		initSwiper(){
			const swiper = new Swiper('.swiper', {
				// Optional parameters
				loop: true,
				pagination: {
					el: '.swiper-pagination',
				},
				autoplay: {
					delay: 5000,
				},
			});
		},
		// 案例详情
		goToDetail(data){
        //   this.$router.push({path:'./case/detail'});
			this.$router.push({
				name:'caseDetail',
				query:{
					routeParams:data
				}
			});
      	},
		// 案例展示
		showCase(id,title){
			// this.$router.push({path:'./case/show'});
			this.$router.push({
				name:'caseShow',
				query:{
					id:id,
					title:title
				}
			});
		},
		// 返回
		goBack(){
			this.$route.go(-1);
			// this.$router.back(-1);
		},
		// 案例列表
		getCaseListData(){
			var _this = this;
			var postUrl = hostUrl + "case/list";

			axios({
					method: 'post',
					url: postUrl,
					data:{
						"isCase": "1",
						"pageNumber": "1",
						"pageSize": "20",
						"timestamp": ""
					},
					headers:{
						token:localStorage.token
					}
			})
			.then(function (response) {
				if(response.data.status){
					// console.log("case:");
					// console.log(response);
					_this.caseDataList = response.data.objectData.pageList;
				}
				else{
					artMsg(response.data.message);
				}
			})
			.catch(function (error) {
				console.log(error);
			});
		}
    },
}
</script>
    <style>
	    body{	
			margin:0;
		}
		.fl {
			float: left!important;
		}
		.fr {
			float: right!important;
		}
		.cl:after {
			visibility: hidden;
			display: block;
			font-size: 0;
			content: " ";
			clear: both;
			height: 0
		}
		.cl {
			*zoom: 1
		}
		.main{
			background:#fff;
		}
		.main-bg{
			position:relative;
			width:100%;
			overflow-x: hidden;
		}
		.main-bg .main-title{
			position: absolute;
			top: 73px;
			right: 15px;
			color: #fff;
			font-size: 17px;
			z-index: 9;
		}
		.menu{
			zoom: 0.85;
		}
		.menu .menu-link{
			position:relative;
			width:33.33%;
			height:100%;
		}
		.menu .menu-link > a{
			position: relative;
		    width:68.5px;
			height:68.5px;	
			border-radius:100%;
			margin: 37px auto 0 auto;
			display:block;
		}
		.menu .menu-link:nth-child(1) > a{
		    background: -webkit-linear-gradient(#43cbf1, #1e87df);
			box-shadow:0 3px 10px rgba(30,135,223,.5);
		}
		.menu .menu-link:nth-child(2) > a{
		    background: -webkit-linear-gradient(#fc9b58, #f84326);
			box-shadow:0 3px 10px rgba(248,67,38,0.5);
		}
		.menu .menu-link:nth-child(3) > a{
		    background: -webkit-linear-gradient(#8edd35, #3eaf17);
			box-shadow:0 3px 10px rgba(62,175,23,0.5);
		}
		.menu .case-share-ico{
		    position:absolute;
			top:calc(50% - 15px);
			left:calc(50% - 15.5px);
			width:30px;
			height:31px;
			background-image:url(data:image/png;base64,);
			display:block;
		}
		.menu .planning-results-ico{
		    position:absolute;
			top:calc(50% - 15px);
			left:calc(50% - 15px);
			width:30px;
			height:30px;
			background-image:url(data:image/png;base64,);
			display:block;
		}
		.menu .topic-searche-ico{
		    position:absolute;
			top:calc(50% - 15px);
			left:calc(50% - 13px);
			width:29px;
			height:33px;
			background-image:url(data:image/png;base64,);
			display:block;
		}
		.menu .menu-title{
			color:#333;
			font-size:18px;
			text-align:center;
			margin-top:7px;
		}
        .share-list{
			background-color:#f9fbfd;
			margin-bottom: 50px;
		}
		.share-list .share-item{
			width:calc(100% - 40px);
			background:#fff;
			margin:26px 20px 0 20px;
			border-radius:7px;
			display:block;
			overflow:hidden;
			box-shadow: 0 3px 10px #ddd;
		}
		.share-list .share-box{
			padding: 20px 10px 20px 30px;
			zoom: 0.86;
		}
		.share-list .share-l{
			width:115px;
			height:115px;
			border-radius:7px;
			overflow:hidden;
		}
		.share-list .case-title{
			color:#666;
			font-size:16px;
            text-align: left;
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
		}
		.share-list .share-l{
			margin-right:14px;
		}
		.share-list .share-r{
			position:relative;
		    width: 58%;
			height: 115px;
		}
		.share-list .case-other{
			position:absolute;
			right:0;
			bottom:0;
			width: 100%;
		}
		.share-list .date{
			position: relative;
			top: 2px;
			color:#999;
			font-size:12px;
		}
		.case-list{
			height:30px;
			line-height:30px;
		    color:#333;
			font-size:24px;
			margin-top: 30px;
			margin-left:20px;
		}
		.case-list > div{
		    float:left;
		}
		.case-list > div:nth-child(1){
		    width:6px;
			height:100%;
			background: -webkit-linear-gradient(#35c1f4, #3465fc);
			margin-right:17px;
		}
		.swiper {
			width: 100%;
		}
		.swiper-pagination{
			bottom:25px;
		}
    </style>



