<template>
    <div class="show-case">
        <div id="caseTitle" class="case-title">{{caseTitle}}</div>
        <div class="case-filename">{{pdfFlieName}}</div>
        <div class="case-img">
            <img :src="pdfData" width="100%">
            <div class="case-opt-menu">
                <div><span class="dwn-btn-ico2"></span>文件下载</div>
                <div><span class="share-btn-ico2"></span>分享</div>
                <div><span class="share-link-ico"></span>共享链接</div>
                <div><span class="wx_ico2"></span>发送微信</div>
                <div><span class="friends-ico2"></span>朋友圈</div>
                <a href="javascript:void(0)" class="close-circle-btn" @click="closeMenu($event)">x</a>
            </div>
            <div class="case-opt-menu tree-menu">
                <ul id="treeRoot">
                    <tree class="item" :item="treeData"></tree>
                </ul>
                <a href="javascript:void(0)" class="close-circle-btn" @click="closeMenu($event)">x</a>
            </div>
        </div>
        <div class="case-content">{{caseContent}}</div>
        <div class="go-top-btn" @click="goTop()">
            <span class="go-top-ico"></span>
        </div>
    </div>
</template>

<script>
import tree from '@/components/common/tree'

export default {
    components:{
        tree
    },
  data () {
    return {
        caseTitle:'费县梁邱镇邵庄村”多规合一“村庄规划文本',
        pdfFlieName:'文件：附件.pdf',
        pdfData:'data:image/jpg;base64,',
        caseContent:'正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容，正文文案部分文字内容。。。',
        treeData : {
            name: "村庄规划",
            children: [
                { name: "1.镇政府申请" },
                { name: "2.专家评审意见" },
                { name: "3.村民代表大会决议" },
                { name: "4.县自资源主管部门审查意见" },
                { name: "5.公示材料" },
                {
                    name: "6.规委会审查意见",
                    children: [
                        { name: "费县梁邱镇邵庄村多规合一村..." },
                        { name: "梁邱镇邵庄村多规合一村" }
                    ]
                },
                {
                    name: "7.文本、说明、图件、数据",
                    children: [
                        { name: "1.费县梁邱镇邵庄村‘多规合一村’" },
                        { name: "2.费县梁邱镇邵庄村‘多规合一村’" },
                        { name: "3.图件" },
                        {
                            name: "4.数据",
                            children: [{ name: "aaa" }, { name: "bbb" },{ name: "ccc" }, { name: "ddd" }]
                        }
                    ]
                },

            ]
        }
    }
  },
  methods:{
      goTop(){
          document.documentElement.scrollTop = 0;
      },
      closeMenu(event){
          var tar = event.currentTarget;
          tar.parentElement.style.display = 'none';
      },
  }
}
</script>
<style>
body{
    background:#f2f4f5;
}
.show-case{
    width:90%;
    background:#fff;
    padding:0 5%;
    margin:16px 0;
}
.case-title,
.case-content{
    color:#393939;
    font-size:16px;
    text-align:left;
    padding-top:25px;
}
.case-filename{
    color:#ccc;
    font-size:14px;
    text-align:left;
    margin-top:15px;
    margin-bottom:10px;
}
.case-img{
    position:relative;
}
.case-content{
    font-size:14px;
    text-indent:28px;
    padding-bottom:20px;
}
.go-top-btn{
    position:fixed;
    right:5%;
    bottom:5%;
    padding:15px;
    background:#fff;
    border:1px solid #ccc;
    border-radius: 100%;
    box-shadow: 2px 2px 5px #ddd;
}
.case-opt-menu{
    position:absolute;
    right:0;
    bottom:0;
    background:#fff;
    box-shadow:2px 2px 2px #ccc;
    padding:10px;
    border-radius:5px;
}
.case-opt-menu>div{
    line-height:normal;
    text-align:left;
    padding-bottom:7px;
}
.case-opt-menu>div:last-child{
    padding-bottom:0;
}
.case-opt-menu>div>span{
    margin-left:3px;
    margin-right:10px;
}
.close-circle-btn{
    position: absolute;
    top: -5px;
    right: -12px;
    width: 25px;
    height: 25px;
    line-height: 25px;
    color: #fff;
    font-size: 12px;
    background: rgba(57,57,57,.5);
    border-radius: 100%;
    display: block;
}
.tree-menu{
    top: 45px;
    left: 0;
    right:auto;
    bottom:auto;
    min-width: 40%;
    min-height: 50%;
    text-align: left;
    text-indent: 10px;
}
#treeRoot{
    width: 100%;
    max-height: 300px;
    overflow-x: hidden;
    overflow-y: auto;
}
#treeRoot ul{
    margin-left:10px;
}
#treeRoot li{
    margin-top:10px;
}
::-webkit-scrollbar{
    width: 5px;
}
</style>



