import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login/index'
import forgot from '@/components/login/forgot'
import register from '@/components/login/register'
import main from '@/components/mainchange'
import map from '@/components/map/index'

import caseShare from '@/components/case/index'
import caseDetail from '@/components/case/detail'
import caseShow from '@/components/case/show'

import planningResult from '@/components/planning/index'
import planningDetail from '@/components/planning/detail'
import planningShow from '@/components/planning/show'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'login',
            component: login
        },
        {
            path: '/register',
            name: 'register',
            component: register
        },
        {
            path: '/forgot',
            name: 'forgot',
            component: forgot
        },
        {
            path: '/map',
            name: 'map',
            component: map
        },
        {
            path: '/main',
            name: 'main',
            component: main
        },
        {
            path: '/case/index',
            name: 'caseShare',
            component: caseShare
        },
        {
            path: '/case/detail',
            name: 'caseDetail',
            component: caseDetail
        },
        {
            path: '/case/show',
            name: 'caseShow',
            component: caseShow
        },
        {
            path: '/planning/index',
            name: 'planningResult',
            component: planningResult
        },
        {
            path: '/planning/detail',
            name: 'planningDetail',
            component: planningDetail
        },
        {
            path: '/planning/show',
            name: 'planningShow',
            component: planningShow
        }
    ]
})


