<template>
  <div>
    <div class="control-content">
      <div class="control-box" v-show="controlBoxDispFlg">
        <a href="javascript:void(0)" :class="[layerCtrlDispFlg ? 'toolbox-txt-clicked' : '']" @click="openLayerSetupBox()">
          <div :class="[layerCtrlDispFlg ? 'layer-ctrl-clicked-ico':'layer-ctrl-ico']"></div>
          <div>图层控制</div>
        </a>
        <!-- <a href="javascript:void(0)">
					<div class="special-exhibition-ico"></div>
					<div>专题展示</div>
				</a> -->
        <a href="javascript:void(0)" @click="toolClicked()" :class="[countBoxDispFlg ? 'toolbox-txt-clicked' : '']">
          <div :class="[countBoxDispFlg ? 'tool-clicked-ico' : 'tool-ico']"></div>
          <div>工具</div>
        </a>
        <!-- <a href="javascript:void(0)">
					<div class="points-of-interes-ico"></div>
					<div>兴趣点</div>
				</a>
				<a href="javascript:void(0)" @click="track()">
					<div class="track-ico"></div>
					<div>轨迹</div>
				</a> -->
        <!-- <a href="javascript:void(0)" @click="switchLayer()">
					<div class="pieces-of-map-ico"></div>
					<div>所在图斑</div>
				</a>
				<a href="javascript:void(0)" >
					<div class="search-near-ico"></div>
					<div>搜附近</div>
				</a> -->
        <a href="javascript:void(0)" :class="[mapQueryBtnFlg ? 'toolbox-txt-clicked' : '']">
          <div :class="[mapQueryBtnFlg ? 'pieces-of-map-clicked-ico' : 'pieces-of-map-ico']" @click="mapQueryBtnClicked()"></div>
          <div>地图查询</div>
        </a>
        <a href="javascript:void(0)" :class="[mapCountBtnFlg ? 'toolbox-txt-clicked' : '']">
          <div :class="[mapCountBtnFlg ? 'count-bar-clicked-ico' : 'count-bar-ico']" @click="mapCountBtnClicked()"></div>
          <div>地图统计</div>
        </a>
        <a href="javascript:void(0)" :class="[iAndEBtnBtnFlg ? 'toolbox-txt-clicked' : '']">
          <div :class="[iAndEBtnBtnFlg ? 'camera-clicked-ico' : 'camera-black-ico']" @click="iAndEBtnClicked()"></div>
          <div>村民监督</div>
        </a>

        <a href="javascript:void(0)" @click="clearAll()">
          <div class="delete-ico"></div>
          <div>清除</div>
        </a>
        <!-- 
				<a href="javascript:void(0)" @click="addMarker()"><i class="fa fa-map-marker"></i></a>
				<a href="javascript:void(0)" @click="measure('LineString')"><i class="fa fa-pencil"></i></a>
				<a href="javascript:void(0)" @click="measure('area')"><i class="fa fa-square"></i></a>
				<a href="javascript:void(0)" @click="clear()"><i class="fa fa-trash-o"></i></a>
				-->
      </div>
      <div class="control-box" v-show="controlBoxDispFlg2">
        <!-- <a href="javascript:void(0)" :class="[layerCtrlDispFlg ? 'toolbox-txt-clicked' : '']" @click="openLayerSetupBox()">
					<div :class="[layerCtrlDispFlg ? 'layer-ctrl-clicked-ico':'layer-ctrl-ico']"></div>
					<div>图层控制</div>
				</a> -->
        <!-- <a href="javascript:void(0)">
					<div class="special-exhibition-ico"></div>
					<div>专题展示</div>
				</a> -->
        <!-- <a href="javascript:void(0)" @click="toolClicked()" :class="[countBoxDispFlg ? 'toolbox-txt-clicked' : '']">
					<div :class="[countBoxDispFlg ? 'tool-clicked-ico' : 'tool-ico']"></div>
					<div>工具</div>
				</a> -->
        <!-- <a href="javascript:void(0)">
					<div class="points-of-interes-ico"></div>
					<div>兴趣点</div>
				</a>
				<a href="javascript:void(0)" @click="track()">
					<div class="track-ico"></div>
					<div>轨迹</div>
				</a> -->
        <!-- <a href="javascript:void(0)" @click="switchLayer()">
					<div class="pieces-of-map-ico"></div>
					<div>所在图斑</div>
				</a>
				<a href="javascript:void(0)" >
					<div class="search-near-ico"></div>
					<div>搜附近</div>
				</a> -->
        <!-- <a href="javascript:void(0)" :class="[mapQueryBtnFlg ? 'toolbox-txt-clicked' : '']">
					<div :class="[mapQueryBtnFlg ? 'pieces-of-map-clicked-ico' : 'pieces-of-map-ico']" @click="mapQueryBtnClicked()"></div>
					<div>地图查询</div>
				</a>
				<a href="javascript:void(0)" :class="[mapCountBtnFlg ? 'toolbox-txt-clicked' : '']">
					<div :class="[mapCountBtnFlg ? 'count-bar-clicked-ico' : 'count-bar-ico']" @click="mapCountBtnClicked()"></div>
					<div>地图统计</div>
				</a>
				<a href="javascript:void(0)" :class="[iAndEBtnBtnFlg ? 'toolbox-txt-clicked' : '']">
					<div :class="[iAndEBtnBtnFlg ? 'camera-clicked-ico' : 'camera-black-ico']" @click="iAndEBtnClicked()"></div>
					<div>村民监督</div>
				</a> -->
        <a href="javascript:void(0)" :class="[mapPlanBtnBtnFlg ? 'toolbox-txt-clicked' : '']">
          <div :class="[mapQueryBtnFlg ? 'pieces-of-map-clicked-ico' : 'pieces-of-map-ico']" @click="mapPlanBtnClicked()"></div>
          <div>规划选址</div>
        </a>
        <a href="javascript:void(0)" @click="clearAll()">
          <div class="delete-ico"></div>
          <div>清除</div>
        </a>
        <!-- 
				<a href="javascript:void(0)" @click="addMarker()"><i class="fa fa-map-marker"></i></a>
				<a href="javascript:void(0)" @click="measure('LineString')"><i class="fa fa-pencil"></i></a>
				<a href="javascript:void(0)" @click="measure('area')"><i class="fa fa-square"></i></a>
				<a href="javascript:void(0)" @click="clear()"><i class="fa fa-trash-o"></i></a>
				-->
      </div>
      <div class="count-box cl" v-show="countBoxDispFlg">
        <div class="fl" @click="initMeasure('LineString')">
          <div class="distance-count-ico"></div>
          <div>距离量算</div>
        </div>
        <div class="fl" @click="initMeasure('area')">
          <div class="area-count-ico"></div>
          <div>面积量算</div>
        </div>
      </div>
    </div>
    <layerCtrlBox ref="layerCtrlBox"></layerCtrlBox>
    <div class="home-btn" @click="goHome()">
      <span class="home-btn-ico"></span>
    </div>
    <div class="back-btn" @click="goBack()" v-show="showBackBtnFlg">
      <span class="back-btn-ico"></span>
    </div>
  </div>
</template>

<script>
import Draw from 'ol/interaction/Draw';
import Map from 'ol/Map';
import Overlay from 'ol/Overlay';
import View from 'ol/View';
import { Circle as CircleStyle, Fill, Stroke, Style } from 'ol/style';
import { LineString, Polygon } from 'ol/geom';
import { OSM, Vector as VectorSource } from 'ol/source';
import { Tile as TileLayer, Vector as VectorLayer } from 'ol/layer';
import { getArea, getLength } from 'ol/sphere';
import { unByKey } from 'ol/Observable';
import GeoJSON from 'ol/format/GeoJSON';
import {artMsg } from "./common/common";
import layerCtrlBox from './layerCtrlBox.vue';

export default {
  name: 'ctrlBox',
  props: ['parentHandler'],
  components: {
    layerCtrlBox
  },
  data() {
    return {
      draw: null,
      switchLayerFlg: false,
      controlBoxDispFlg: false,
      controlBoxDispFlg2: false,
      layerCtrlDispFlg: false,
      countBoxDispFlg: false,
      mapQueryBtnFlg: false,
      mapCountBtnFlg: false,
      iAndEBtnBtnFlg: false,
      mapPlanBtnBtnFlg: true,
      showBackBtnFlg: false
    }
  },
  created() {
    if (this.$route.query.surpervison) {
      this.controlBoxDispFlg = true
      this.controlBoxDispFlg2 = false
      this.$parent.mapPlanBoxFlg = false
    } else {
      this.controlBoxDispFlg2 = true
      this.controlBoxDispFlg = false
    }
  },
  methods: {
    // 回城
    goHome() {
      this.$parent.setHomePosition();
    },
    // 返回上一级
    goBack() {
      this.$parent.clickBack();
    },
    // 初始化量算
    initMeasure(type) {
      this.clearAll();
      this.measure(type);
    },
    // 关闭所有工具条按钮样式
    closeAllToolBtnStyle() {
      this.layerCtrlDispFlg =
        this.countBoxDispFlg =
        this.mapQueryBtnFlg =
        this.mapQueryBtnFlg2 =
        this.mapCountBtnFlg =
        this.iAndEBtnBtnFlg = false;
      this.$refs.layerCtrlBox.closeLayerSetupBox()
    },
    // 村民监督
    iAndEBtnClicked() {
      this.closeAllToolBtnStyle();
      this.iAndEBtnBtnFlg = true;
      this.$parent.dyqzBoxDispFLg = true;
      this.$parent.surveyClick();
    },
    // 规划选址
    mapPlanBtnClicked() {
      // this.closeAllToomapPlanClicklBtnStyle();  
      // this.clearAll();
      this.mapPlanBtnBtnFlg = !this.mapPlanBtnBtnFlg;
      // if(!this.mapPlanBtnBtnFlg){
      // 	this.$parent.mapPlanBoxFlg=false
      // 	// this.$parent.controlx()
      // }
      this.$parent.mapPlanBoxFlg = !this.$parent.mapPlanBoxFlg

      console.log(this.$parent.mapPlanBoxFlg);
      // this.$parent.mapPlanBtnClicked();
    },
    // 地图统计
    mapCountBtnClicked() {
      this.clearAll();
      this.mapCountBtnFlg = true;
      this.$parent.mapCountClick();
      this.$parent.clearAllBottomBtnStyle();
    },
    // 地图查询
    mapQueryBtnClicked() {
      this.clearAll();
      this.mapQueryBtnFlg = true;
      this.$parent.mapQueryClick();
      this.$parent.clearAllBottomBtnStyle();
    },
    // 点击工具按钮
    toolClicked() {
      this.closeAllToolBtnStyle();
      this.countBoxDispFlg = true;
      this.$parent.closeAllThirdMenuBox();
    },
    // 打开 图层控制 弹窗
    openLayerSetupBox() {
      // if(this.layerCtrlDispFlg){
      // 	this.$refs.layerCtrlBox.closeLayerSetupBox();
      // }
      // else{
      if (!this.$parent.currentTbLayer&&this.$parent.isOffline) {
        artMsg("当前图层无法切换");
        return
      }
      this.clearAll();
      this.closeAllToolBtnStyle();
      this.layerCtrlDispFlg = true;
      this.$refs.layerCtrlBox.openLayerSetupBox();
      this.$parent.mapQueryClick();
      this.$parent.clearAllBottomBtnStyle();
      // }

    },
    // 清除地图上所有覆盖物
    clearAll() {
      this.$parent.clearAllBottomBtnStyle();
      this.$parent.source.clear();
      this.$parent.map.removeInteraction(this.draw);
      this.$parent.map.getOverlays().clear();
      this.closeAllToolBtnStyle();
      this.$parent.closeAllThirdMenuBox();
      this.$parent.map.removeLayer(this.$parent.vecLayer2);
      this.$parent.map.removeLayer(this.$parent.vecLayer3);
      this.$parent.clearres()
    },
    // 显示工具条
    dispCtrlBox() {
      this.controlBoxDispFlg = true;
    },
    // 隐藏工具条
    hideCtrlBox() {
      this.controlBoxDispFlg = false;
    },
    // open layerCtrlBOx
    layerCtrlBox1() {
      this.$parent.openLayerCtrl();
    },
    // 用户轨迹（GeoJSon）
    track() {
      const geojsonObject = {
        'type': 'FeatureCollection',
        'crs': {
          'type': 'name',
          'properties': {
            'name': 'EPSG:4326',
          },
        },
        'features': [
          {
            'type': 'Feature',
            'geometry': {
              'type': 'GeometryCollection',
              'geometries': [
                {
                  'type': 'LineString',
                  'coordinates': [
                    [-5e6, -5e6],
                    [0, -5e6],
                  ],
                },
              ],
            },
          },
        ],
      };

      const tVectorSource = new VectorSource({
        features: new GeoJSON().readFeatures(geojsonObject),
      });
      const tVectorLayer = new VectorLayer({
        source: tVectorSource,
        zIndex: 1,
        visible: true
      });
      this.parentHandler.map.addLayer(tVectorLayer);
      // const map = new Map({
      // layers: [
      //     new TileLayer({
      //     source: new OSM(),
      //     }),
      //     tVectorLayer,
      // ],
      // target: 'map',
      //     view: new View({
      //         center: [0, 0],
      //         zoom: 2,
      //     }),
      // });
    },
    // 打Marker
    addMarker() {
      var iconFeature = new Feature({
        geometry: new Point([123.430267, 41.814827]),
        name: 'Null Island',
        population: 4000,
        rainfall: 500
      });
      var iconStyle = new Style({
        image: new Icon({
          anchor: [0.5, 0.5],
          anchorXUnits: 'fraction',
          anchorYUnits: 'pixels',
          crossOrigin: 'anonymous',
          src: "@/src/assets/logo.png"
        }),
        // 设置marker的label
        text: new Text({
          textAlign: 'center',
          textBaseline: 'top',
          // font: font,
          offsetX: 0,
          offsetY: 20,
          backgroundFill: new Fill({
            color: '#67C23A'
          }),
          padding: [0, 2, 0, 2],
          // text: resolution < 1040 ? feature.get('index') : '',
          text: "marker",
          fill: new Fill({
            color: 'white'
          }),
        })
      });

      iconFeature.setStyle(iconStyle);

      var vectorSource = new VectorSource({
        features: [iconFeature]
      });

      var vectorLayer = new VectorLayer({
        source: vectorSource
      });

      this.parentHandler.map.addLayer(vectorLayer);
    },
    // 测量
    measure(typeSelect) {
      const _this = this.parentHandler;
      /**
       * Currently drawn feature.
       * @type {import("../src/ol/Feature.js").default}
       */
      let sketch;

      /**
       * The help tooltip element.
       * @type {HTMLElement}
       */
      let helpTooltipElement;

      /**
       * Overlay to show the help messages.
       * @type {Overlay}
       */
      let helpTooltip;

      /**
       * The measure tooltip element.
       * @type {HTMLElement}
       */
      let measureTooltipElement;

      /**
       * Overlay to show the measurement.
       * @type {Overlay}
       */
      let measureTooltip;

      /**
       * Message to show when the user is drawing a polygon.
       * @type {string}
       */
      const continuePolygonMsg = 'Click to continue drawing the polygon';

      /**
       * Message to show when the user is drawing a line.
       * @type {string}
       */
      const continueLineMsg = 'Click to continue drawing the line';

      /**
       * Handle pointer move.
       * @param {import("../src/ol/MapBrowserEvent").default} evt The event.
       */
      const pointerMoveHandler = function (evt) {
        if (evt.dragging) {
          return;
        }
        /** @type {string} */
        let helpMsg = 'Click to start drawing';

        if (sketch) {
          const geom = sketch.getGeometry();
          if (geom instanceof Polygon) {
            helpMsg = continuePolygonMsg;
          } else if (geom instanceof LineString) {
            helpMsg = continueLineMsg;
          }
        }

        helpTooltipElement.innerHTML = helpMsg;
        helpTooltip.setPosition(evt.coordinate);

        helpTooltipElement.classList.remove('hidden');
      };
      _this.map.on('pointermove', pointerMoveHandler);

      _this.map.getViewport().addEventListener('mouseout', function () {
        helpTooltipElement.classList.add('hidden');
      });

      /**
       * Format length output.
       * @param {LineString} line The line.
       * @return {string} The formatted length.
       */
      const formatLength = function (line) {
        const length = getLength(line);
        let output;
        if (length > 100) {
          // output = Math.round((length / 1000) * 100) / 100 + ' ' + '公里';
          output = Math.round((length / 1000) * 100) / 100 / 2 + ' ' + '里地';
        } else {
          output = Math.round(length * 100) / 100 + ' ' + '米';
        }
        return output;
      };

      /**
       * Format area output.
       * @param {Polygon} polygon The polygon.
       * @return {string} Formatted area.
       */
      const formatArea = function (polygon) {
        const area = getArea(polygon);
        let output;
        if (area > 10000) {
          // output = Math.round((area / 1000000) * 100) / 100 + ' ' + '平方公里(平方千米)';
          output = Math.round((area / 1000000) * 100) / 100 * 0.0006667 + ' ' + '亩';
        } else {
          output = Math.round(area * 100) / 100 + ' ' + '平方米';
        }
        return output;
      };

      function addInteraction() {
        const type = typeSelect == 'area' ? 'Polygon' : 'LineString';
        // this.$parent.map.removeInteraction(_this.draw);
        _this.draw = new Draw({
          source: _this.source,
          type: type,
          style: new Style({
            fill: new Fill({
              color: 'rgba(255, 255, 255, 0.2)',
            }),
            stroke: new Stroke({
              color: 'rgba(0, 0, 0, 0.5)',
              lineDash: [10, 10],
              width: 2,
            }),
            image: new CircleStyle({
              radius: 5,
              stroke: new Stroke({
                color: 'rgba(0, 0, 0, 0.7)',
              }),
              fill: new Fill({
                color: 'rgba(255, 255, 255, 0.2)',
              }),
            }),
          }),
        });
        _this.map.addInteraction(_this.draw);
        createMeasureTooltip();
        createHelpTooltip();

        let listener;
        _this.draw.on('drawstart', function (evt) {
          // set sketch
          sketch = evt.feature;

          /** @type {import("../src/ol/coordinate.js").Coordinate|undefined} */
          let tooltipCoord = evt.coordinate;

          listener = sketch.getGeometry().on('change', function (evt) {
            const geom = evt.target;
            let output;
            if (geom instanceof Polygon) {
              output = formatArea(geom);
              tooltipCoord = geom.getInteriorPoint().getCoordinates();
            } else if (geom instanceof LineString) {
              output = formatLength(geom);
              tooltipCoord = geom.getLastCoordinate();
            }
            measureTooltipElement.innerHTML = output;
            measureTooltip.setPosition(tooltipCoord);
          });
        });

        _this.draw.on('drawend', function () {
          measureTooltipElement.className = 'ol-tooltip ol-tooltip-static';
          measureTooltip.setOffset([0, -7]);
          // unset sketch
          sketch = null;
          // unset tooltip so that a new one can be created
          measureTooltipElement = null;
          createMeasureTooltip();
          unByKey(listener);
          // console.log('drawend');
          _this.map.removeInteraction(_this.draw);
        });
      }

      /**
       * Creates a new help tooltip
       */
      function createHelpTooltip() {
        if (helpTooltipElement) {
          helpTooltipElement.parentNode.removeChild(helpTooltipElement);
        }
        helpTooltipElement = document.createElement('div');
        helpTooltipElement.className = 'ol-tooltip hidden';
        helpTooltip = new Overlay({
          element: helpTooltipElement,
          offset: [15, 0],
          positioning: 'center-left',
        });
        _this.map.addOverlay(helpTooltip);
      }

      /**
       * Creates a new measure tooltip
       */
      function createMeasureTooltip() {
        if (measureTooltipElement) {
          measureTooltipElement.parentNode.removeChild(measureTooltipElement);
        }
        measureTooltipElement = document.createElement('div');
        measureTooltipElement.className = 'ol-tooltip ol-tooltip-measure';
        measureTooltip = new Overlay({
          element: measureTooltipElement,
          offset: [0, -15],
          positioning: 'bottom-center',
          stopEvent: false,
          insertFirst: false,
        });
        _this.map.addOverlay(measureTooltip);
      }
      addInteraction();
    },
    // 切换图层
    switchLayer(e) {
      if (this.switchLayerFlg) {
        this.parentHandler.tiandituImageLayer.setVisible(false);
        this.parentHandler.raster.setVisible(true);
      }
      else {
        this.parentHandler.raster.setVisible(false);
        this.parentHandler.tiandituImageLayer.setVisible(true);
      }
      this.switchLayerFlg = !this.switchLayerFlg;
    },
    // 清除
    clear() {
      this.parentHandler.map.removeInteraction(this.parentHandler.draw);
      // this.parentHandler.clearFreeHandDraw();
      // this.parentHandler.map.removeAllOverlay();
    },
  }
}
</script>
<style>
.control-content {
  position: absolute;
  top: 70px;
  width: 100%;
}
.control-box {
  position: absolute;
  top: 0;
  right: 6.4%;
  z-index: 9999;
  zoom: 0.65;
  box-shadow: 1px 1px 2px rgb(0, 0, 0, 0.3);
}

.control-box > a {
  text-align: center;
  background: #fff;
  padding: 10px 10px 0 10px;
  display: block;
}

.control-box > a > div:first-child {
}
.control-box > a > div:last-child {
  color: #393939;
  font-size: 12px;
  text-align: center;
  margin-top: 8px;
  padding-bottom: 16px;
  border-bottom: 1px solid #d2d2d2;
}
.count-box {
  position: absolute;
  top: 70px;
  right: 18%;
  color: #757581;
  font-size: 12px;
  text-align: center;
  background: #fff;
  zoom: 0.65;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.count-box > div {
  padding: 14px 15px 7px 15px;
}
.count-box > div > div {
  margin-bottom: 8px;
}
.home-btn {
  position: absolute;
  top: 25em;
  left: 3em;
  padding: 14px;
  border-radius: 100%;
  background: #fff;
  /* box-shadow:2px 2px 5px #eee; */
  zoom: 0.55;
}
.back-btn {
  position: absolute;
  top: 17em;
  left: 1.6em;
  padding: 6px;
  border-radius: 50%;
  background: #fff;
}
</style>


