            if (response.data.listData && response.data.listData.length > 0) {
              response.data.listData.forEach(item => {
                if (item.version) {
                  that.offlineMapVersionList.push({ packageId: item.packageId, version: item.version });
                }
              })
            }
            localStorage.setItem("offlineMapVersionList", JSON.stringify(that.offlineMapVersionList))
            resolve()
          }
        })
      })
    },
    async initOfflineMapData() {
      //  return new Promise((Resolve, Reject) => {
      await this.getOfflineMapVersion();
      await this.checkLocalOfflineMapDataVersion();
      const that = this;
      if (this.needUpdateList.length > 0) {
        // this.loadingText = "正在检查更新中，请勿退出..."
        this.downLoading = true;
        let pArray = new Array();
        this.needUpdateList.forEach(item => {
          let downUrl = hostUrl + "common/download?fileName=" + item.version + ".zip";
          console.log(downUrl)
          let p = new Promise((resolve, reject) => {
            api.download({
              url: downUrl,
              savePath: 'fs://data/offline/' + item.packageId + "/" + item.version + ".zip",
              report: true,
              cache: true,
              allowResume: true,
              headers: {
                token: localStorage.token
              },
            }, function (response, err) {
              console.log(response)
              if (response.state == 1) {
                that.fs.rmdir({
                  path: 'fs://data/offline-map/' + item.packageId
                }, function (ret, err) {
                  if (ret.status) {
                    console.log("清空了" + item.packageId)
                  }
                  that.zip.unarchive({
                    file: 'fs://data/offline/' + item.packageId + "/" + item.version + ".zip",
                    toPath: 'fs://data/offline/' + item.packageId + "/" + item.version
                  }, function (ret, err) {
                    if (ret.status) {
                      console.log("解压成功", item.version)
                      that.fs.rmdir({//解压成功后删除压缩包
                        path: 'fs://data/offline/' + item.packageId + "/" + item.version + ".zip"
                      }, function (ret2, err) {
                        if (ret2.status) {
                          artMsg(item.packageId + "数据更新完成");
                          resolve(item.packageId + "数据更新完成")
                        }
                      });
                    } else {
                      that.fs.rmdir({
                        path: 'fs://data/offline/' + item.packageId + "/" + item.version + ".zip"
                      }, function (ret, err) {
                      });
                      reject(item.packageId + "数据解压失败")
                    }
                  });
                });
              }
            });
          });
          pArray.push(p);
          console.log("pArray", JSON.stringify(pArray))
        })
        Promise.all(pArray).then(function (results) {
          that.downLoading = false;
          console.log('results----->', JSON.stringify(results));
          //  Resolve()
        }).catch(function (err) {
          console.log(err);
          Reject(err);
        });

      }
      //  else {
      //   Resolve()
      // }
      //  })
    },
    //检查本地离线地图数据包版本
    async checkLocalOfflineMapDataVersion() {
      let that = this;
      return new Promise((resolve, reject) => {
        this.needUpdateList = [];
        if (this.offlineMapVersionList.length > 0) {
          let copylist = JSON.parse(JSON.stringify(this.offlineMapVersionList))
          fsExist(copylist)
        } else {
          resolve()
        }
        function fsExist(offlineMapVersionList) {
          let p = offlineMapVersionList.pop();
          const fsPath = that.getOfflineMapPathByPackageId(p.packageId)
          that.fs.exist({
            path: fsPath
          }, function (ret, err) {
            console.log(fsPath)
            console.log(JSON.stringify(ret))
            if (!ret.exist) {
              that.needUpdateList.push(p);
            }
            if (offlineMapVersionList.length > 0) {
              fsExist(offlineMapVersionList)
            } else {
              console.log("结束check", JSON.stringify(that.needUpdateList))
              resolve()
            }
          })
        }

      })
    },
