    // 饼状图 初始化
    pieChartInit(xData, yData) {
      !!this.myChart ? this.myChart.dispose() : "";
      var chartDom = document.getElementById("mapCountChartContent");
      this.myChart = echarts.init(chartDom);
      var option;

      var dataArr = [];
      for (var i = 0; i < xData.length; i++) {
        var tmpObj = {
          value: yData[i],
          name: xData[i],
        };
        dataArr.push(tmpObj);
      }

      option = {
        title: {
          text: "邵庄村",
          subtext: "土地面积一览（单位：平方米）",
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        series: [
          {
            name: "邵庄村",
            type: "pie",
            radius: [10, 110],
            center: ["50%", "50%"],
            roseType: "area",
            itemStyle: {
              borderRadius: 8,
            },
            data: dataArr,
          },
        ],
      };
      option && this.myChart.setOption(option);
    },
    // 柱状图 初始化
    barChartInit(xData, yData) {
      !!this.myChart ? this.myChart.dispose() : "";
      var chartDom = document.getElementById("mapCountChartContent");
      this.myChart = echarts.init(chartDom);
      var option;

      option = {
        title: {
          text: "邵庄村",
          subtext: "土地面积一览（单位：平方米）",
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          data: xData,
          axisLabel: {
            interval: 0,
            formatter: function (value) {
              return value.split("").join("\n");
            },
          },
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: yData,
            type: "bar",
          },
        ],
      };

      option && this.myChart.setOption(option);
    },
