    getInitPosition() {
      var _this = this;
      _this.BMap.getLocation(
        {
          accuracy: "10m",
          autoStop: true,
          filter: 1,
          enableLocInForeground: false,
          notification: "后台持续定位中",
        },
        function (ret, err) {
          if (ret.status) {
            var lat = ret.lat;
            var lon = ret.lon;
            var tmpObj = CoordinateUtil.getWgs84xy(lon, lat);
            var temArr = [tmpObj.lng, tmpObj.lat];
            // artMsg(temArr);
            _this.currentLatLot = temArr;
            // _this.addPoint(_this.currentLatLot);
            // _this.setViewCenter(_this.currentLatLot);
            _this.setHomePosition();
            _this.trackLatLot.push(temArr);
            // artMsg(JSON.stringify(ret));
            artMsg("定位成功");
          } else {
            // artMsg(err.code);
            console.log("getInitPosition:" + err.code);
          }
          _this.BMap.stopLocation();
        }
      );
    },
    // 连续定位
    continuousPositioning() {
      var _this = this;
      _this.BMap.getLocation(
        {
          accuracy: "1m",
          autoStop: false,
          filter: 1,
          enableLocInForeground: true,
          notification: "后台持续定位中",
        },
        function (ret, err) {
          if (ret.status) {
            artMsg("连续定位成功");
            var lat = ret.lat;
            var lon = ret.lon;
            var tmpObj = CoordinateUtil.getWgs84xy(lon, lat);
            var latlotArr = [tmpObj.lat, tmpObj.lng];
            _this.trackLatLot.push(latlotArr);
            _this.addLine(_this.trackLatLot);
          }
          //  else {
          //   artMsg(err.code);
          // }
        }
      );
    },
    // 渲染信息弹窗
    renderInfoBox(data) {
      var _this = this;
      _this.infoBoxContent.length = 0;
      Object.keys(data).forEach(function (k) {
        var name = "";
        var sk = k.toLowerCase();
        if (sk == "geometry") {
          //不显示坐标信息
          return;
        }
        switch (sk) {
          case "bsm":
            name = "标识码";
            break;
          case "ysdm":
            name = "要素代码";
            break;
          case "tbybh":
            name = "图斑预编号";
            break;
          case "tbbh":
            name = "图斑编号";
            break;
          case "qsxz":
            name = "权属性质";
            break;
          case "qsdwdm":
            name = "权属单位代码";
            break;
          case "qsdwmc":
            name = "权属单位名称";
            break;
          case "zldwdm":
            name = "座落单位代码";
            break;
          case "zldwmc":
            name = "座落单位名称";
            break;
          case "shape_area":
            name = "图斑面积";
            break;
          default:
            name = k;
        }
        var str = {
          contentKey: name,
          contentValue: data[k],
        };
        _this.infoBoxContent.push(str);
      });
      if (!this.lineQueryFlg && !this.areaQueryFlg) {
        this.dispInfoBox();
      }
    },
    // 显示图斑信息弹窗
    dispInfoBox() {
      this.infoBoxDispFlg = true;
    },
    // 关闭图斑信息弹窗
    closeInfoBox() {
      this.infoBoxDispFlg = false;
    },
    // 点击图斑
    patternClick() {
      this.getConnectionType();
      if (this.clickLayerClickedFlg == false) {
        this.clearAllBottomBtnStyle();
        const _this = this;
        if (this.isOffline) {
          //离线
          this.removeMapSingleClickListener();
          this.addOffLineMapClicklistener();
        } else {
          //在线
          this.singleClickListener = this.map.on("singleclick", (evt) => {
            const viewResolution = /** @type {number} */ (
              _this.view.getResolution()
            );
            const url = _this.country1Source.getFeatureInfoUrl(
              evt.coordinate,
              viewResolution,
              "EPSG:4326",
              { INFO_FORMAT: "application/json" }
            );
            if (url) {
              fetch(url)
                .then((response) => response.text())
                .then((json) => {
                  var result = JSON.parse(json);
                  if (result.features[0]) {
                    var data = result.features[0].properties;
                    _this.renderInfoBox(data);
                  }
                });
            }
          });
        }
      } else {
        this.removeMapSingleClickListener();
        if (!this.currentTbLayer) {//不是图斑层，下钻监听
          this.addOffLineMapClickNextlistener();
        }

      }
      this.clickLayerClickedFlg = !this.clickLayerClickedFlg;
    },
