        this.$refs.ctrlBox.hideCtrlBox();
        this.$refs.walkArea.dispAllMenus();
        this.hideMapZoom();
        this.clearAllBottomBtnStyle();
      }
      this.walkAreaUploadFlg = !this.walkAreaUploadFlg;
    },

    // 绘制区域上传：手绘
    freeHandDraw(val) {
      const _this = this;
      _this.source.clear();
      _this.handDraws = [];
      this.freeHandDrawObj = new Draw({
        source: _this.source,
        type: val,
        freehand: true,
      });
      this.freeHandDrawObj.on("drawend", function (e) {
        // var geom = e.feature.getGeometry().transform('EPSG:3857', 'EPSG:4326');
        // _this.coordinatesArr = e.feature.getGeometry().transform('EPSG:3857', 'EPSG:4326');
        _this.coordinatesArr = e.feature.getGeometry().getCoordinates();
        _this.handDraws.push(_this.coordinatesArr);
        console.log(_this.coordinatesArr, _this.handDraws.length);
        // console.log(geom)
      });
      this.map.addInteraction(this.freeHandDrawObj);
    },

