    // 通过包名获取离线地图共通路径
    getOfflineMapPathByPackageId(packageId) {
      const offlineMap = this.offlineMapVersionList.filter(item => item.packageId == packageId)[0];
      // const path = "./../static/offline/" + offlineMap.packageId + "/";
      const path = "file://" + api.fsDir + "/data/offline/" + offlineMap.packageId + "/" + offlineMap.version + "/" + packageId + "/";
      return path
    },
