    // 移除所有图层,只显示name图层
    removeAllLayersExcept(name) {
      console.log(111,name)
      const layers = this.map.getLayers().getArray().slice();
      for (let index = 0; index < layers.length; index++) {
        const layer = layers[index]
        if (layer._name !== 'handDraw') {//保留手绘层
          if (name && layer._name === name) {
            layer.setVisible(true);
            layer.setOpacity(1);//初始化图层透明度为1
          } else {
            layer.setVisible(false);
          }
        }
      }
      this.$refs.ctrlBox.$refs.layerCtrlBox.initOfflineLayerList();//初始化图层设置
    },
