        this.currentFeature.setStyle(null);
      }
      const layers = this.map.getLayers().getArray().slice();
      for (let index = 0; index < layers.length; index++) {
        const layer = layers[index]
        console.log(name, isVisible)
        if (layer._name === name) {
          layer.setVisible(isVisible);
        }
      }
    },
    // 设置name图层透明度    
