    /**
    * 为离线地图注册鼠标单击下钻的监听
    */
    addOffLineMapClickNextlistener() {
      const that = this;
      this.singleClickListener = this.map.on("singleclick", (event) => {
        this.map.forEachFeatureAtPixel(event.pixel, (feature) => {
          // 为移动到的feature发送自定义的singleclick消息
          if (feature.values_.DM || feature.values_.dm) {
            const dm = feature.values_.DM ? feature.values_.DM : feature.values_.dm
            let islast = dm.length > 6 ? true : false;//图斑名称长度大于6
            let existLayers = [];
            if (this.offlineLayerOpenList.length > 0) {
              existLayers = this.offlineLayerOpenList.filter(item => item.name.toString().split("_")[1] == dm);
            }
            if (existLayers[0]) {
              that.$refs.ctrlBox.showBackBtnFlg = true;
              console.log(existLayers)
              that.removeAllLayersExcept(existLayers[0].name);
              handle(existLayers[0].name, true);
            } else {
              const name = islast ? "tdxz_" + dm : dm;
              const layer_name = islast ? "tdxz_" + dm + "_1" : name;
              const name2 = islast ? "czgh_" + dm : dm;
              const layer_name2 = islast ? "czgh_" + dm + "_1" : name;
              const fsPath = that.getOfflineMapPathByPackageId(islast ? "geojson_tdxz" : "geojson_common") + layer_name + ".geojson";
              that.fs.exist({
                path: fsPath
              }, function (ret, err) {
                if (ret.exist) {
                  that.removeAllLayersExcept();
                  if (islast) {
                    // const path = that.getOfflineMapPathByPackageId("geojson_tdxz")
                    // const layer1 = that.createGeoJsonLayer("geojson_" + name, path + name + "_1.geojson", true, false);
                    // const layer2 = that.createGeoJsonLayer("geojson_" + name, path + name + "_2.geojson", true, false);
                    // const layer3 = that.createGeoJsonLayer("geojson_" + name, path + name + "_3.geojson", true, false);
                    // const path2 = that.getOfflineMapPathByPackageId("geojson_czgh")
                    // const layer4 = that.createGeoJsonLayer("geojson_" + name2, path2 + name2 + "_1.geojson", false, false);
                    // const layer5 = that.createGeoJsonLayer("geojson_" + name2, path2 + name2 + "_2.geojson", false, false);
                    // const layer6 = that.createGeoJsonLayer("geojson_" + name2, path2 + name2 + "_3.geojson", false, false);
                    // addLayer(layer1)
                    // addLayer(layer2)
                    // addLayer(layer3)
                    // addLayer(layer4)
                    // addLayer(layer5)
                    // addLayer(layer6)
                  } else {
                    const path = that.getOfflineMapPathByPackageId("geojson_common") + name + ".geojson"
                    const layer = that.createGeoJsonLayer(name, path, true, islast);
                    that.loading = true;
                    that.loadingText = "加载中..."
                    layer.on('postrender', evt => {
                      that.loading = false;
                    })
                    that.map.addLayer(layer);
                  }
                  handle(name, false);
                } else {
                  artMsg("暂无数据");
                }
              })
            }
            function addLayer(layer) {
              that.loading = true;
              that.loadingText = "加载中..."
              layer.on('postrender', evt => {
                that.loading = false;
              });
              that.map.addLayer(layer);
            };
            function handle(name, isExist) {
              console.log(name, isExist, islast)
              that.$refs.ctrlBox.showBackBtnFlg = true;
              const extent = feature.getGeometry().getExtent()
              const center = getCenter(extent);
              that.map.getView().setCenter(center);  //设置当前地图的显示中心位置
              that.map.getView().fit(extent);// 自适应缩放级别
              const zoom = that.map.getView().getZoom();
              if (dm) {
                that.offlineLayerOpenOrderList.push({ name: name, center: center, zoom: zoom, islast: islast });
                isExist ? '' : that.offlineLayerOpenList.push({ name: name, center: center, zoom: zoom, islast: islast });
              }
              that.currentTbLayer = null;
              if (islast) {//图斑最后一级
                if (!isExist) {
                  const path = that.getOfflineMapPathByPackageId("layer_tdxz");
                  let imglayer = that.createImgLayer("tdxz_" + dm, path + "tdxz_" + dm + "/{z}/{x}/{y}.png", true, zoom, 10, 17)
                  that.offlineLayerOpenList.push({ name: "tdxz_" + dm, center: center, zoom: zoom, islast: islast });
                  that.map.addLayer(imglayer);
                  const path2 = that.getOfflineMapPathByPackageId("layer_czgh");
                  let imglayer2 = that.createImgLayer("czgh_" + dm, path2 + "czgh_" + dm + "/{z}/{x}/{y}.png", false, zoom, 10, 17)
                  that.offlineLayerOpenList.push({ name: "czgh_" + dm, center: center, zoom: zoom, islast: islast });
                  that.map.addLayer(imglayer2);
                }
                if (that.currentFeature) {
                  that.currentFeature.setStyle(null);
                }
                that.currentTbLayer = dm;
                that.removeMapSingleClickListener();
                that.addOffLineMapClicklistener();
              }
            }
          }
        });
      });
    },

    /**
     * 为离线地图注册鼠标单击事件的监听
     */
    addOffLineMapClicklistener() {
      this.currentFeature = new Feature();
      let currentFeatureList = [];
      this.singleClickListener = this.map.on("singleclick", (event) => {
        this.closeInfoBox();
        this.currentFeature.setStyle(null);
        currentFeatureList = [];
        let hasVectorFlag = false;
        const layers = this.map.getLayers().getArray().slice();
        for (let index = 0; index < layers.length; index++) {
          const layer = layers[index]
          if (layer._type == "geojson" && layer.getVisible()) {
            console.log("hasVectorFlag", hasVectorFlag)
            hasVectorFlag = true;
          }
        }
        if (!hasVectorFlag && this.isOffline) {
          artMsg("当前没有矢量图层，无法查询");
          return
        }
        this.map.forEachFeatureAtPixel(event.pixel, (feature) => {
          if (currentFeatureList.length == 1) {// 防止多个feature选中
            return
          }
          this.currentFeature = feature;
          this.currentFeature.setStyle(null);
          currentFeatureList.push(this.currentFeature)
          // 为移动到的feature发送自定义的singleclick消息
          if (this.currentFeature.values_) {
            this.renderInfoBox(this.currentFeature.values_);
            this.currentFeature.dispatchEvent({ type: "singleclick", event: event });
            const style = new Style({
              stroke: new Stroke({
                color: 'rgba(255,92,92, 1)',
                width: 4
              }),
              fill: new Fill({
                color: 'rgba(255,92,92, 0.5)',
              })
            })
            this.currentFeature.setStyle(style);
          } else {
            this.closeInfoBox();
          }
        });
      });
    },
    /**
     * 移除地图鼠标单击事件的监听
     */
    removeMapSingleClickListener() {
      this.map.un(
        this.singleClickListener.type,
        this.singleClickListener.listener
      );
    },
    /**
     * 创建geoJsonLayer
     */
    createGeoJsonLayer(name, url, isVisible, isOpacity) {
      const style = isOpacity ? new Style({
        fill: new Fill({
          color: "rgba(255, 255, 255, 0)", //设置透明
        }),
        stroke: new Stroke({
          color: "rgba(255, 255, 255, 0)",
          width: 0,
        }),
      }) : undefined;
      const geoJsonLayer = new VectorLayer({
        projection: "EPSG:4326",
        source: new VectorSource({
          url: url,
          format: new GeoJSON(),
        }),
        style: style,
        visible: isVisible,
        zIndex: 100,
      });
      geoJsonLayer._type = "geojson"
      geoJsonLayer._name = name
      return geoJsonLayer
    },
    /**
     * 创建离线瓦片图层
     */
    createImgLayer(name, url, isVisible, zoom, minZoom, maxZoom) {
      let imglayer = new TileLayer({
        source: new XYZ({
          url: url,
          tilePixelRatio: 1,
          zoom: zoom,
          minZoom: minZoom,
          maxZoom: maxZoom,
        }),
        visible: isVisible
      });
      imglayer._type = "img"
      imglayer._name = name
      return imglayer
    },
    // 打marker
    addMarker(mercator) {
      !!this.imgLayer ? this.map.removeLayer(this.imgLayer) : "";
      // 创建marker
      var proj = "EPSG:4326";
      var point = new Point(mercator);
      const iconFeature = new Feature({
        geometry: point,
        name: "Null Island",
        population: 4000,
        rainfall: 500,
        projection: "EPSG:4326",
      });
      // 设置style
      const iconStyle = new Style({
        image: new Icon({
          anchor: [0.5, 46],
          anchorXUnits: "fraction",
          anchorYUnits: "pixels",
          src: this.currentPosiIconUrl,
          // src: "https://openlayers.org/en/v3.20.1/examples/data/icon.png",
        }),
      });

      iconFeature.setStyle(iconStyle);

      const vectorSource = new VectorSource({
        features: [iconFeature],
      });

      this.imgLayer = new VectorLayer({
        source: vectorSource,
        zIndex: 4,
        visible: true,
      });

      this.map.addLayer(this.imgLayer);

      // 设置地图中心，将地图移动到中心点
      // var viewPoint = this.mercatorTolonlat(mercator);
      // console.log("viewPoint:" + viewPoint);
      var viewPoint = [117.721318, 35.155238];
      var view = this.map.getView();
      view.setCenter(viewPoint, 9, "EPSG:4326");
    },
    // 地图查询
    mapQuery(type) {
      if (this.isOffline) {
        artMsg("网络无连接，暂不可用");
        return;
      }
      let draw, snap; // global so we can remove them later
      let value = type;
      var _this = this;
      const source = new VectorSource();
      const style = new Style({
        fill: new Fill({
          color: "rgba(51, 204, 51, 0.2)",
        }),
        stroke: new Stroke({
          color: "#33cc33",
          width: 2,
        }),
        image: new CircleStyle({
          radius: 7,
          fill: new Fill({
            color: "#ffcc33",
          }),
        }),
      });

      !!this.vecLayer2 ? this.map.removeLayer(this.vecLayer2) : "";

      this.vecLayer2 = new VectorLayer({
        source: source,
        style: function (feature) {
          const geometry = feature.getGeometry();
          return geometry.getType() === "GeometryCollection"
            ? geodesicStyle
            : style;
        },
        zIndex: 999,
      });

      this.map.addLayer(this.vecLayer2);

      this.drawObj = new Draw({
        source: source,
        type: value,
      });
      this.map.addInteraction(this.drawObj);
      snap = new Snap({ source: source });
      this.map.addInteraction(snap);
      // console.log(this.map.getLayers().array_);

      this.drawObj.on("drawend", function (evt) {
        var wfsUrl = "http://47.115.204.188:8089/geoserver/country/wms";
        _this.map.removeInteraction(_this.drawObj);
        // 空间查询
        _this.shapeQuery(evt.feature, wfsUrl);
      });
    },
