    },
    // 墨卡托转经纬度
    mercatorTolonlat(mercator) {
      let lonlat = [];

      let x = (mercator[0] / 20037508.34) * 180;
      let y = (mercator[1] / 20037508.34) * 180;

      y =
        (180 / Math.PI) *
        (2 * Math.atan(Math.exp((y * Math.PI) / 180)) - Math.PI / 2);

      lonlat.push(x);
      lonlat.push(y);
      console.log("lonlat:" + lonlat);
      return lonlat;
    },
    // 经纬度转墨卡托
    lonlatToMercator(lonLat) {
      var mercator = [];

      let x = (lonLat.lon * 20037508.34) / 180;
      let y =
        Math.log(Math.tan(((90 + lonLat.lat) * Math.PI) / 360)) /
        (Math.PI / 180);

      y = (y * 20037508.34) / 180;

      // mercator.x = x;
      // mercator.y = y;
      mercator.push(x);
      mercator.push(y);

      return mercator;
    },
    // 切换视图中心点(经纬度array)
    setViewCenter(viewPoint) {
      var view = this.map.getView();
      view.setCenter(viewPoint, 9, "EPSG:4326");
    },
    // 打点（圆点）
    addPoint(lnglat) {
      !!this.pointLayer ? this.map.removeLayer(this.pointLayer) : "";
      var point = new Feature({
        geometry: new Point(lnglat),
        projection: "EPSG:4326",
      });
      //构点
      this.pointLayer = new VectorLayer({
        source: new VectorSource({
          features: [point],
          projection: "EPSG:4326",
        }),
        style: new Style({
          image: new CircleStyle({
            radius: 4,
            fill: new Fill({
              color: "blue",
            }),
          }),
        }),
        zIndex: 9,
        visible: true,
      });
      this.map.addLayer(this.pointLayer);
    },
    // 绘制轨迹
    addLine(pointsList) {
      var roadLine = new LineString(pointsList);
      var roadLineSource = new VectorSource({
        features: [new Feature(roadLine)],
        projection: "EPSG:4326",
      });
      var roadLineLayer = new VectorLayer({
        source: roadLineSource,
        style: new Style({
          stroke: new Stroke({
            color: "red",
            width: 3,
          }),
        }),
        zIndex: 9,
        visible: true,
      });
      this.map.addLayer(roadLineLayer);
    },
    // 绘制点击表格项回显轨迹
    addLine1(pointsList) {
      this.map.removeLayer(this.roadLineLayer1);
      var roadLine = new LineString(pointsList);
      var roadLineSource = new VectorSource({
        features: [new Feature(roadLine)],
        projection: "EPSG:4326",
      });
      this.roadLineLayer1 = new VectorLayer({
        source: roadLineSource,
        style: new Style({
          stroke: new Stroke({
            color: "red",
            width: 6,
          }),
        }),
        zIndex: 99,
        visible: true,
      });
      this.map.addLayer(this.roadLineLayer1);
      if (pointsList.length !== 0) {
        this.setViewCenter(
          this.mercatorTolonlat([pointsList[0][0], pointsList[0][1]])
        );
      }
    },
    // 历史监督管理
    historyManage() {
      this.getConnectionType();
      if (this.isOffline) {
        artMsg("网络无连接,暂不可用。");
        return
      }
      this.dyqzCloseAllBox();
      if (!this.historyManageFlg) {
        this.$refs.ctrlBox.hideCtrlBox();
        this.$refs.takePhoto.hideAllMenus();
        this.hideMapZoom();
        this.clearAllBottomBtnStyle();
        this.$refs.his.getDataList(1);
        this.$refs.his.dispHistoryWin();
      }
      this.historyManageFlg = !this.historyManageFlg;
      // this.$refs.his.historyBoxDispFLg = !this.$refs.his.historyBoxDispFLg;
    },
    // 草稿监督管理
    localHistoryManage() {
      this.dyqzCloseAllBox();
      if (!this.localHistoryManageFlg) {
        this.$refs.ctrlBox.hideCtrlBox();
        this.$refs.takePhoto.hideAllMenus();
        this.hideMapZoom();
        this.clearAllBottomBtnStyle();
        this.$refs.his.dispLocalHistoryWin();
      }
      this.localHistoryManageFlg = !this.localHistoryManageFlg;
    },
    // 高亮显示
    showHightLight(v, k) {
      this.ischeckone = k;
      for (let index = 0; index < this.chartXData1.length; index++) {
        if (
          v.values_.Shape_Area == this.chartXData1[index].values_.Shape_Area
        ) {
          this.checkeddata = this.chartXData1[index];
        }
      }
      var dataArr = this.checkeddata.values_["geometry"].flatCoordinates;
      var lonLatArrs = [];
      for (var i = 0; i < dataArr.length; i += 2) {
        var tmpArr = [dataArr[i], dataArr[i + 1]];
        lonLatArrs.push(tmpArr);
      }
      this.addLine1(lonLatArrs);
      // console.log(lonLatArrs);
      this.$refs.takePhoto.addLineString(lonLatArrs);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- <style src="@/assets/css/ol.css"></style> -->
<style src="@/assets/css/map.css"></style>
<style src="@/assets/css/menu.css"></style>
<style scoped>
.box {
  height: 15px;
  position: relative;
  -webkit-box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.3);
}
.box > span {
  display: block;
  height: 100%;
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    color-stop(0, #63de4e),
    color-stop(1, #34a702)
  );
  -webkit-box-shadow: inset 0 2px 9px rgba(255, 255, 255, 0.3),
    inset 0 -2px 6px rgba(0, 0, 0, 0.4);
  position: relative;
  overflow: hidden;
  font-size: 10px;
  color: #393939;
}
.animate > span span {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: -webkit-gradient(
    linear,
    0 0,
    100% 100%,
    color-stop(0.25, rgba(255, 255, 255, 0.2)),
    color-stop(0.25, transparent),
    color-stop(0.5, transparent),
    color-stop(0.5, rgba(255, 255, 255, 0.2)),
    color-stop(0.75, rgba(255, 255, 255, 0.2)),
    color-stop(0.75, transparent),
    to(transparent)
  );
  z-index: 2;
  -webkit-background-size: 40px;
  -webkit-animation: move 2s linear infinite;
  overflow: hidden;
}

@-webkit-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 50px 50px;
  }
}
</style>



