<template>
  <div class="forgot-box" id="forgot">
    <div v-if="!userid">
      <div class="forgot-title">忘记密码？</div>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model.trim="ruleForm.phone" placeholder="请输入手机号码"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="identifyCode">
          <el-input v-model="ruleForm.identifyCode" placeholder="请输入验证码"></el-input>
        </el-form-item>
        <!--验证码组件-->
        <div @click="refreshCode()">
          <Sidentify :identifyCode="identifyCode"></Sidentify>
        </div>
        <el-button type="primary" @click="foundpwd()" class="submit-btn">找回密码</el-button>
      </el-form>
    </div>
    <div v-if="userid">
      <div class="forgot-title">重置密码</div>
      <el-form :model="passwordForm" :rules="rules" ref="passwordForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="请输入密码" prop="password">
          <el-input v-model.trim="passwordForm.password" placeholder="8-20位字母、数字或特殊字符组合" type="password"></el-input>
        </el-form-item>
        <el-form-item prop="password2">
          <el-input v-model.trim="passwordForm.password2" placeholder="请再次输入密码" type="password"></el-input>
        </el-form-item>
        <el-button type="primary" @click="resetpwd()" class="submit-btn">重置密码</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import Sidentify from "@/components/common/sidentify";
import { hostUrl, artMsg } from "../common/common";
import axios from "axios";
export default {
  components: { Sidentify },
  data() {
    var phoneValidator = (rule, value, callback) => {
      var reg =
        /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
      if (value === "") {
        callback(new Error("请输入手机号码"));
      } else if (!reg.test(value)) {
        callback(new Error("请输入正确手机号码"));
      } else {
        callback();
      }
    };
    var identifyCodeValidator = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入验证码"));
      } else if (this.identifyCode !== value) {
        callback(new Error("请输入正确验证码"));
        this.refreshCode();
      } else {
        callback();
      }
    };
    var passwordValidator = (rule, value, callback) => {
      const pwd = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$/
      if (value === "") {
        callback(new Error("请输入密码"));
      } else if (!pwd.test(value)) {
        callback("密码格式不正确");
      } else {
        callback();
      }
    }
    return {
      identifyCodes: "1234567890",
      identifyCode: "",
      userid: '11',
      ruleForm: {
        phone: "",
        identifyCode: "",
      },
      passwordForm: {
        password: "",
        password2: ""
      },
      rules: {
        phone: [
          {
            required: true,
            trigger: "blur",
            validator: phoneValidator,
          },
        ],
        identifyCode: [
          { required: true, trigger: "blur", validator: identifyCodeValidator },
        ],
        password: [
          { required: true, trigger: "blur", validator: passwordValidator },
        ],
        password2: [
          { required: true, trigger: "blur", validator: passwordValidator },
        ]
      },
    };
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
  methods: {
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode +=
          this.identifyCodes[this.randomNum(0, this.identifyCodes.length)];
      }
    },
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    foundpwd() {
      if (
        this.ruleForm.phone.length == 0 ||
        this.ruleForm.identifyCode.length == 0
      ) {
        this.refreshCode();
        console.log("电话号或验证码不能为空");
        artMsg("电话号或验证码不能为空");
        return
      } else {
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
            this.checkPhoneRegistern();
            console.log("验证成功");
          }
        });
      }
    },
    // 验证手机号是否注册
    checkPhoneRegistern() {
      const postUrl = hostUrl + "verify";
      axios({
        method: "post",
        url: postUrl,
        data: {
          pageNumber: "1",
          pageSize: "1",
          phone: this.phone,
        },
        headers: {
          token: localStorage.token,
        },
      }).then(res => {
        if (!res.status) {
          console.log("手机号未注册，请先注册");
          artMsg("手机号未注册，请先注册");
        } else {
          this.userid = res.objectData.userId;
        }
      })
    },
    resetpwd() {
      if (this.passwordForm.password == "" || this.passwordForm.password2 == "") {
        console.log("密码不能为空");
        artMsg("密码不能为空");
        return
      }
      if (this.passwordForm.password !== this.passwordForm.password2) {
        console.log("两次输入的密码不一致");
        artMsg("两次输入的密码不一致");
        return
      }
      const postUrl = hostUrl + "resetPassword";
      axios({
        method: "post",
        url: postUrl,
        data: {
          userId: this.userid,
          password: this.password,
        },
        headers: {
          token: localStorage.token,
        },
      }).then(res => {
        if (res.status) {
          console.log(res.message);
          artMsg("密码修改成功，请重新登录");
          this.$router.push({ path: "/login" });
        }
      })
    }
  },
};
</script>
<style>
.forgot-box {
  position: relative;
  margin-right: 6.6%;
  margin-left: 6.6%;
}
.forgot-title {
  color: #020433;
  font-size: 30px;
  text-align: left;
  margin-top: 48px;
  margin-bottom: 35px;
}
.submit-btn {
  margin-top: 100px;
  width: 200px;
}
</style>
       



