<template>
  <div class="show-case">
    <div id="caseTitle" class="case-title" @click="openAndClose()">
      {{ caseTitle }}
      <span
        class="triangle-down"
        :class="[triangleUpDownFlg ? 'turn-up' : '']"
      ></span>
    </div>
    <div class="case-filename">{{ pdfFlieName }}</div>
    <div class="pdfViewer" ref="showCaseBox">
      <!-- <el-button-group>
            <el-button type="primary" icon="el-icon-arrow-left" size="mini" @click="prePage">上一页</el-button>
            <el-button type="primary" size="mini" @click="nextPage">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
            </el-button-group>
            <div style="marginTop: 10px; color: #409EFF">{{ pageNum }} / {{ pageTotalNum }}</div> -->
      <div class="songlist-row-s">
        <!-- <pdf ref="pdf" :src="pdfData" v-for="i in numPages" :key="i" :page="i"> </pdf> -->
          <PreviewpdfH5 ref="pdf"></PreviewpdfH5>
      </div>

      <!-- <pdf
                :page="pageNum"
                :src="pdfData"
                @progress="loadedRatio = $event"
                @num-pages="pageTotalNum=$event"
                style="width:100%;height:100%;">
            </pdf> -->
    </div>
    <div class="case-opt-menu">
      <div><span class="dwn-btn-ico2"></span>下载</div>
      <div><span class="share-btn-ico2"></span>分享</div>
      <div><span class="share-link-ico"></span>共享</div>
      <div><span class="wx_ico2"></span>发送</div>
      <div><span class="friends-ico2"></span>朋友圈</div>
      <!-- <a href="javascript:void(0)" class="close-circle-btn" @click="closeMenu($event)">x</a> -->
    </div>
    <div id="treeMenu" class="case-opt-menu tree-menu">
      <ul id="treeRoot">
        <tree class="item" :item="treeData"></tree>
      </ul>
      <!-- <a href="javascript:void(0)" class="close-circle-btn" @click="closeMenu($event)">x</a> -->
    </div>
    <div class="case-content">{{ caseContent }}</div>
    <!-- <div class="go-top-btn" @click="goTop()">
            <span class="go-top-ico"></span>
        </div> -->
  </div>
</template>

<script>
import tree from "@/components/common/tree";

import axios from "axios";
import { hostUrl, artMsg } from "../common/common";
import PreviewpdfH5 from "./pdfInfo.vue";
import pdf from "vue-pdf";

export default {
  name: "show",
  components: {
    tree,
    PreviewpdfH5,
    pdf,
  },
  provide() {
    return {
      changePdfContent: this.changePdfContent,
    };
  },
  data() {
    return {
      id: "",
      caseTitle: "",
      pdfFlieName: "",
      pdfData: "",
      caseContent: "",
      treeData: {},
      numPages: null, // pdf 总页数
      pageNum: 1,
      pageTotalNum: 1, // 总页数
      loadedRatio: 0, // 当前页面的加载进度，范围是0-1 ，等于1的时候代表当前页已经完全加载完成了
      triangleUpDownFlg: false,
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.getData(this.id);
  },
  updated() {
    this.scrollGoTop();
  },
  methods: {
    // 初始化纵向滚动条
    scrollGoTop() {
      // let page = document.getElementsByClassName('pdfViewer')[0];
      // page.scrollIntoView()

      // 动画效果
      // page.scrollIntoView({ behavior: 'smooth' })
      this.$nextTick(() => {
        this.$refs.showCaseBox.scrollTop = 0;
      });
      // this.$refs.showCaseBox.scrollIntoView();
    },
    // 显示/隐藏 树形菜单
    openAndClose() {
      if (this.triangleUpDownFlg) {
        this.closeSelectDialog("treeMenu");
      } else {
        this.openSelectDialog("treeMenu");
      }
    },
    // 计算pdf页码总数
    getNumPages() {
      let loadingTask = pdf.createLoadingTask(this.pdfData);
      loadingTask.promise
        .then((pdf) => {
          this.numPages = pdf.numPages;
        })
        .catch((err) => {
          console.error("pdf 加载失败", err);
        });
    },
    // 上一页
    prePage() {
      let page = this.pageNum;
      page = page > 1 ? page - 1 : this.pageTotalNum;
      this.pageNum = page;
    },

    // 下一页
    nextPage() {
      let page = this.pageNum;
      page = page < this.pageTotalNum ? page + 1 : 1;
      this.pageNum = page;
    },
    goTop() {
      document.documentElement.scrollTop = 0;
    },
    closeMenu(event) {
      var tar = event.currentTarget;
      tar.parentElement.style.display = "none";
    },
    openSelectDialog(id) {
      var tar = document.getElementById(id);
      tar.style.display = "block";
      this.triangleUpDownFlg = !this.triangleUpDownFlg;
    },
    closeSelectDialog(id) {
      var tar = document.getElementById(id);
      tar.style.display = "none";
      this.triangleUpDownFlg = !this.triangleUpDownFlg;
    },
    getData(id) {
      var _this = this;
      var postUrl = hostUrl + "case/tree";
      axios({
        method: "post",
        url: postUrl,
        data: {
          planAchieveId: id,
          timestamp: "",
        },
        headers: {
          token: localStorage.token,
        },
      })
        .then(function (response) {
          if (response.data.status) {
            console.log(response.data.objectData);
            if (response.data.objectData.fileList.length > 0) {
              _this.treeData = response.data.objectData.fileList[0];
              _this.caseTitle = response.data.objectData.title;
              _this.pdfFlieName =
                _this.treeData.childList[7].childList[0].label;
              _this.pdfData = _this.treeData.childList[7].childList[0].value;
              _this.$refs.pdf.openPdf(_this.pdfData);
              // _this.getNumPages();

              // loadingTask = pdf.createLoadingTask(url);
              // _this.pdfRender();
              //_this.renderPdf(url);
            } else {
              artMsg("暂无数据");
            }
            // _this.dataList = response.data.objectData.pageList;
          } else {
            // alert(response.data.message);
            artMsg(response.data.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 改变pdf Title和内容
    changePdfContent(title, path) {
      this.pdfFlieName = title;
      this.pdfData = path;
      //引用组件：
      this.$refs.pdf.openPdf(path);
      this.closeSelectDialog("treeMenu");
      this.getNumPages();
      return false;
    },
  },
};
</script>
<style scoped>
body {
  background: #fff;
  height: 100%;
  margin: 0;
  padding: 0;
}
.show-case {
  position: relative;
  top: 0;
  width: 90%;
  background: #fff;
  /* padding: 16px 5%; */
  padding: 0 5% 0 5%;
  margin: 0 0;
  overflow: hidden;
}
.case-title {
  position: relative;
  color: #393939;
  height: 20px;
  line-height: 20px;
  text-indent: 10px;
  font-size: 16px;
  text-align: left;
}
.case-content {
  color: #393939;
  font-size: 16px;
  text-align: left;
  padding-top: 25px;
}
.case-filename {
  color: #ccc;
  font-size: 14px;
  text-align: left;
  margin-top: 15px;
  margin-bottom: 10px;
}
.case-img {
  position: relative;
}
.case-content {
  font-size: 14px;
  text-indent: 28px;
  padding-bottom: 20px;
}
.go-top-btn {
  position: fixed;
  right: 5%;
  bottom: 5%;
  padding: 15px;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 100%;
  box-shadow: 2px 2px 5px #ddd;
}
.case-opt-menu {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  background: #fff;
  padding-bottom: 5px;
}
.case-opt-menu > div {
  line-height: normal;
  text-align: left;
  margin-right: 10px;
  display: inline-block;
}
.case-opt-menu > div:last-child {
  padding-bottom: 0;
}
.case-opt-menu > div > span {
  margin-left: 3px;
}
.close-circle-btn {
  position: absolute;
  top: -5px;
  right: -5px;
  width: 25px;
  height: 25px;
  line-height: 25px;
  color: #fff;
  font-size: 12px;
  background: rgba(57, 57, 57, 0.5);
  border-radius: 100%;
  display: block;
}
.tree-menu {
  position: absolute !important;
  top: 55px;
  left: 0;
  right: auto;
  bottom: auto;
  width: calc(85% - 2px);
  min-height: 50%;
  text-align: left;
  text-indent: 10px;
  margin-left: 5%;
  border: 1px solid #ccc;
  display: none;
}
#treeRoot {
  width: 100%;
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
}
#treeRoot ul {
  margin-left: 10px;
}
#treeRoot li {
  margin-top: 10px;
}
::-webkit-scrollbar {
  width: 5px;
}
.pdfViewer {
  position: relative;
  top: 30px;
  width: 100%;
  height: 500px;
  border: 1px solid #eee;
  box-shadow: 2px 2px 5px #eee;
  overflow-x: hidden;
  overflow-y: auto;
}
.triangle-down {
  position: absolute;
  top: 40px;
  right: 5px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 8px solid #000;
  display: inline-block;
}
.turn-up {
  transform: rotateX(180deg);
}
</style>



