<template>
    <li>
        <template v-if="isFolder">
          <div class="bold" @click="toggle">
              <span class="folder-ico" ></span>
              {{ item.label }}
          </div>
        </template>
        <template v-else>
          <div @click="clickPdf(item.label,item.value)">
            <span class="file-ico"></span>
            {{ item.label }}
          </div>
        </template>
              
              <!-- <span v-if="isFolder">[{{ isOpen ? '-' : '+' }}]</span> -->
        
        <ul v-show="isOpen" v-if="isFolder">
          <tree
            class="item"
            v-for="(child, index) in item.childList"
            :key="index"
            :item="child"
          ></tree>
        </ul>
    </li>
</template>

<script>
export default {
	name:"tree",
  props:{item: Object},
  data(){
      return {
          isOpen: true,
      }
  },
  inject:[
      'changePdfContent'
  ],
	computed: {
		isFolder: function() {
			return this.item.childList && this.item.childList.length;
		}
	},
  methods:{	
    toggle: function() {
      if (this.isFolder) {
        this.isOpen = !this.isOpen;
      }
    },
    // 改变pdf Title和内容
    clickPdf(title,path){
        this.changePdfContent(title,path);
    },
  }
}
</script>
<style></style>


