<template>
  <div class="gray-bg" @click="closeLayerSetupBox()" v-show="layerCtrlBoxDispFlg">
    <div class="layer-ctrl-box" :class="[layerCtrlBoxDispFlg ? 'layerCtrlAnimate' : '']" v-if="!isOffline">
      <div>
        <div class="layer-ctrl-title">地图类型</div>
        <div class="map-type-box cl">
          <div class="fl">
            <div class="vector-layer" :class="[vectorLayerFlg ? 'selected-layer' : '']" @click.stop="vectorLayerClick()">
              <div :class="[vectorLayerFlg ? 'ok-blue-ico' : '']"></div>
            </div>
            <div class="layer-map-txt">矢量图</div>
          </div>
          <div class="fl">
            <div class="image-layer" :class="[imageLayerFlg ? 'selected-layer' : '']" @click.stop="imageLayerClick()">
              <div :class="[imageLayerFlg ? 'ok-blue-ico' : '']"></div>
            </div>
            <div class="layer-map-txt">影像图</div>
          </div>
          <div class="fl">
            <div class="analysis-layer" :class="[currentSituationAnalysisFlg ? 'selected-layer' : '']" @click.stop="currentSituationAnalysisClick()">
              <div :class="[currentSituationAnalysisFlg ? 'ok-blue-ico' : '']"></div>
            </div>
            <div class="layer-map-txt">土地现状</div>
          </div>
          <div class="fl">
            <div class="future-layer" :class="[futurePlanningFlg ? 'selected-layer' : '']" @click.stop="futurePlanningClick()">
              <div :class="[futurePlanningFlg ? 'ok-blue-ico' : '']"></div>
            </div>
            <div class="layer-map-txt">村庄规划</div>
          </div>
        </div>
        <div class="layer-ctrl-title">图层显示</div>
        <div class="layer-item" v-show="vectorLayerFlg">
          <div class="layer-info cl">
            <div class="fl" :class="[this.layerList[0].display ? 'checked-ico':'uncheck-ico']" @click.stop="selectLayer(0)"></div>
            <div class="fl">{{this.layerList[0].layerName}}</div>
            <div class="setup-gray-ico fr" :class="[this.layerList[0].layerSetupFlg ? 'setup-blue-ico':'setup-gray-ico']" @click.stop="setupLayer(0)"></div>
          </div>
          <div class="opcity-box" v-show="this.layerList[0].opacitySetupFlg">
            <span>透明度</span>
            <input type="range" step="0.1" min="0" max="1" @change.stop="setOpcity($event,0)" :value="this.layerList[0].opacity">
          </div>
        </div>
        <div class="layer-item" v-show="imageLayerFlg">
          <div class="layer-info cl">
            <div class="fl" :class="[this.layerList[1].display ? 'checked-ico':'uncheck-ico']" @click.stop="selectLayer(1)"></div>
            <div class="fl">{{this.layerList[1].layerName}}</div>
            <div class="setup-gray-ico fr" :class="[this.layerList[1].layerSetupFlg ? 'setup-blue-ico':'setup-gray-ico']" @click.stop="setupLayer(1)"></div>
          </div>
          <div class="opcity-box" v-show="this.layerList[1].opacitySetupFlg">
            <span>透明度</span>
            <input type="range" step="0.1" min="0" max="1" @change.stop="setOpcity($event,1)" :value="this.layerList[1].opacity">
          </div>
        </div>
        <div class="layer-item" v-show="currentSituationAnalysisFlg">
          <div class="layer-info cl">
            <div class="fl" :class="[this.layerList[2].display ? 'checked-ico':'uncheck-ico']" @click.stop="selectLayer(2)"></div>
            <div class="fl">{{this.layerList[2].layerName}}</div>
            <div class="setup-gray-ico fr" :class="[this.layerList[2].layerSetupFlg ? 'setup-blue-ico':'setup-gray-ico']" @click.stop="setupLayer(2)"></div>
          </div>
          <div class="opcity-box" v-show="this.layerList[2].opacitySetupFlg">
            <span>透明度</span>
            <input type="range" step="0.1" min="0" max="1" @change.stop="setOpcity($event,2)" :value="this.layerList[2].opacity">
          </div>
        </div>
        <div class="layer-item" v-show="futurePlanningFlg">
          <div class="layer-info cl">
            <div class="fl" :class="[this.layerList[3].display ? 'checked-ico':'uncheck-ico']" @click.stop="selectLayer(3)"></div>
            <div class="fl">{{this.layerList[3].layerName}}</div>
            <div class="setup-gray-ico fr" :class="[this.layerList[3].layerSetupFlg ? 'setup-blue-ico':'setup-gray-ico']" @click.stop="setupLayer(3)"></div>
          </div>
          <div class="opcity-box" v-show="this.layerList[3].opacitySetupFlg">
            <span>透明度</span>
            <input type="range" step="0.1" min="0" max="1" @change.stop="setOpcity($event,3)" :value="this.layerList[3].opacity">
          </div>
        </div>
      </div>
    </div>
    <div class="layer-ctrl-box" :class="[layerCtrlBoxDispFlg ? 'layerCtrlAnimate' : '']" v-if="isOffline" @click.stop>
      <div>
        <div class="layer-ctrl-title">地图类型</div>
        <div class="layer-ctrl-option" v-for="(layer,index) in offlineLayerList" :key="index">
          <el-checkbox v-model="layer.display"  @change="checkboxChange($event,layer)">{{layer.layerName}}</el-checkbox>
          <div class="opcity-box mt-10">
            <span>透明度</span>
            <input type="range" step="0.1" min="0" max="1" @change.stop="setOfflineLayerOpcity($event,layer)" :value="layer.opacity">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'layerCtrlBox',
  props: ['parentHandler'],
  data() {
    return {
      layerList: [
        {
          layerName: '天地图矢量底图',
          display: true,
          opacity: 1,
          opacitySetupFlg: false,
          layerSetupFlg: false,
        },
        {
          layerName: '天地图影像',
          display: false,
          opacity: 1,
          opacitySetupFlg: false,
          layerSetupFlg: false,
        },
        {
          layerName: '2020土地利用现状',
          display: true,
          opacity: 1,
          opacitySetupFlg: false,
          layerSetupFlg: false,
        },
        {
          layerName: '2020村庄规划',
          display: false,
          opacity: 1,
          opacitySetupFlg: false,
          layerSetupFlg: false,
        },
      ],
      offlineLayerList: [
        {
          layerName: '土地现状矢量图',
          display: true,
          opacity: 1,
          type: "geojson"
        },
        {
          layerName: '土地现状瓦片图',
          display: false,
          opacity: 1,
          type: "img"
        },
        {
          layerName: '村庄规划矢量图',
          display: false,
          opacity: 1,
          type: "geojson"
        },
        {
          layerName: '村庄规划瓦片图',
          display: false,
          opacity: 1,
          type: "img"
        },
      ],
      layerCtrlBoxDispFlg: false,
      vectorLayerFlg: false,
      imageLayerFlg: false,
      currentSituationAnalysisFlg: false,
      futurePlanningFlg: false,
      isOffline: false,
      switchType: true
    }
  },
  mounted() {
    this.getConnectionType();
  },
  methods: {
    initOfflineLayerList() {
      console.log("初始化图层设置")
      this.offlineLayerList = [
        {
          layerName: '土地现状瓦片图',
          display: true,
          opacity: 1,
        },
        {
          layerName: '村庄规划瓦片图',
          display: false,
          opacity: 1
        },
      ]
    },
    checkboxChange(event, layer) {
      let name = ""
      switch (layer.layerName) {
        case "土地现状瓦片图":
          name = 'tdxz_' + this.$parent.$parent.currentTbLayer
          break;
        case "村庄规划瓦片图":
          name = 'czgh_' + this.$parent.$parent.currentTbLayer
          break;
      }
      this.$parent.$parent.setVisibleLayer(name, event)
    },
    setOfflineLayerOpcity(event, layer) {
      const tar = event.currentTarget;
      const tarVal = parseFloat(tar.value);
      layer.opacity = tarVal;
      let name = ""
      switch (layer.layerName) {
        case "土地现状瓦片图":
          name = 'tdxz_' + this.$parent.$parent.currentTbLayer
          break;
        case "村庄规划瓦片图":
          name = 'czgh_' + this.$parent.$parent.currentTbLayer
          break;
      }
      this.$parent.$parent.setOpacityLayer(name, tarVal)
    },
    // 获取手机网络连接类型
    getConnectionType() {
      if (api.connectionType == "none") {
        this.isOffline = true
      } else {
        this.isOffline = false;
      }
    },

    // 清除所有图层选择样式
    clearAllLayerSelectedStyle() {
      this.vectorLayerFlg =
        this.imageLayerFlg =
        this.currentSituationAnalysisFlg =
        this.futurePlanningFlg = false;
    },
    // 选择矢量图层
    vectorLayerClick() {
      this.clearAllLayerSelectedStyle();
      this.vectorLayerFlg = !this.vectorLayerFlg;
    },
    // 选择影像图层
    imageLayerClick() {
      this.clearAllLayerSelectedStyle();
      this.imageLayerFlg = !this.imageLayerFlg;

    },
    // 选择现状分析
    currentSituationAnalysisClick() {
      this.clearAllLayerSelectedStyle();
      this.currentSituationAnalysisFlg = !this.currentSituationAnalysisFlg;
    },
    // 选择未来规划
    futurePlanningClick() {
      this.clearAllLayerSelectedStyle();
      this.futurePlanningFlg = !this.futurePlanningFlg;
    },
    // 设置透明度
    setOpcity(event, index) {
      var tar = event.currentTarget;
      var tarVal = parseFloat(tar.value);
      this.$parent.$parent.layerList[index].setOpacity(tarVal);
      this.layerList[index].opacity = tarVal;
    },
    // 设置/关闭 图层透明度 弹窗
    setupLayer(index) {
      if (this.layerList[index].display) {
        this.layerList[index].layerSetupFlg = !this.layerList[index].layerSetupFlg;
        this.layerList[index].opacitySetupFlg = !this.layerList[index].opacitySetupFlg;
      }
    },
    // 选中/关闭 图层
    selectLayer(index) {
      this.layerList[index].display = !this.layerList[index].display;
      this.layerList[index].layerSetupFlg = this.layerList[index].opacitySetupFlg = false;
      this.$parent.$parent.layerList[index].setVisible(this.layerList[index].display);

    },
    // 关闭 图层设置弹窗
    closeLayerSetupBox() {
      this.layerCtrlBoxDispFlg = false;
      this.$parent.layerCtrlDispFlg = false;
    },
    // 打开 图层设置弹窗
    openLayerSetupBox() {
      this.layerCtrlBoxDispFlg = true;
    },
  }
}
</script>
<style>
.layer-control-box,
.info-box {
  position: absolute;
  right: 20%;
  background: white;
  border-radius: 8px;
  overflow: hidden;
}
/* @media screen and (min-width: 500px) {
  .layer-control-box,
  .info-box {
    width: 400px !important;
  }
} */
/* .layer-control-box > div .info-box > div {
  width: calc(100% - 40px);
  height: calc(100% - 20px);
  padding: 10px 20px;
  overflow-x: hidden;
  overflow-y: auto;
}
.layer-control-box > div > .layer-item:first-child {
  margin-top: 20px;
} */
.layer-item {
  margin-bottom: 20px;
}
.layer-info {
  color: #393939;
  font-size: 14px;
  text-align: left;
  margin-bottom: 20px;
}
.layer-info > div:first-child {
  position: relative;
  top: 3px;
  margin-right: 7px;
}
.layer-info > div:last-child {
  position: relative;
  top: 3px;
}
.opcity-box {
  color: #7d7d85;
  font-size: 10px;
}
.opcity-box > span {
  position: relative;
  top: -4px;
  margin-right: 10px;
  margin-left: 5px;
}
.opcity-box > input {
  position: relative;
  top: -5px;
}
.opcity-box > input[type="range"] {
  -webkit-appearance: none;
  width: 70%;
  border-radius: 10px; /*这个属性设置使填充进度条时的图形为圆角*/
}
.opcity-box > input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
}
.opcity-box > input[type="range"]::-webkit-slider-runnable-track {
  height: 3px;
  border-radius: 5px; /*将轨道设为圆角的*/
  background-image: linear-gradient(to right, #35c2f4, #3465fc);
}

.opcity-box > input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 12px;
  width: 12px;
  margin-top: -5px;
  background: #0079ff;
  border-radius: 50%;
}
.layer-list-close-btn {
  position: absolute;
  right: 15px;
  z-index: 99;
}
.layer-ctrl-box {
  position: absolute;
  top: 0;
  right: 0;
  width: 57.77%;
  height: 100%;
  color: #fff;
  font-size: 14px;
  background: rgba(255, 255, 255, 1);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  z-index: 99991;
  overflow: hidden;
}
.layer-ctrl-box > div {
  width: calc(100% - 9.455% - 9.455%);
  height: calc(100% - 1.83% - 1.83%);
  padding: 20px 9.455%;
}
.layer-ctrl-item {
  margin-top: 20px;
  margin-bottom: 20px;
}
.layer-ctrl-title {
  color: #393939;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: left;
}
.map-type-box {
  margin-bottom: 36px;
}
.map-type-box > div {
  position: relative;
  width: 50%;
  text-align: center;
  margin-top: 10px;
}
.vector-layer {
  width: 58px;
  height: 64px;
  background-image: url(data:image/png;base64,);
  border-radius: 20px;
  border: 1px solid transparent;
  display: inline-block;
  margin-bottom: 10px;
}
.image-layer {
  width: 58px;
  height: 64px;
  background-image: url(data:image/png;base64,);
  border-radius: 20px;
  border: 1px solid transparent;
  display: inline-block;
  margin-bottom: 10px;
}
.analysis-layer {
  width: 58px;
  height: 64px;
  background-image: url(data:image/png;base64,);
  border-radius: 20px;
  border: 1px solid transparent;
  display: inline-block;
  margin-bottom: 10px;
}
.future-layer {
  width: 58px;
  height: 64px;
  background-image: url(data:image/png;base64,);
  border-radius: 20px;
  border: 1px solid transparent;
  display: inline-block;
  margin-bottom: 10px;
}
.layer-map-txt {
  color: #393939;
  font-size: 12px;
  text-align: center;
}
.selected-layer {
  border-color: #346afc;
}
.ok-blue-ico {
  position: absolute;
  right: 10px;
  bottom: 25px;
  width: 15px;
  height: 15px;
  background-image: url(data:image/png;base64,);
}
@keyframes moveLeft {
  from {
    right: -57.77%;
  }
  to {
    right: 0;
  }
}

@-webkit-keyframes moveLeft /*Safari and Chrome*/ {
  from {
    right: -57.77%;
  }
  to {
    right: 0;
  }
}
.layerCtrlAnimate {
  animation: moveLeft 0.5s linear;
  -webkit-animation: moveLeft 0.5s linear; /*Safari and Chrome*/
}
.mt-10 {
  margin-top: 10px;
}
</style>


